import React from 'react';
import { useTranslation } from 'react-i18next';
import { maritalStatus } from '../../config/FilterOptions';
import { IFilterGroupContainer } from 'estreeh-common-library/src/interfaces/IFilterGroupContainer';
import FilterCheckboxItem from './FilterCheckboxItem';
import FilterGroup from './FilterGroup';

const FGMaritalStatus = (props:IFilterGroupContainer) => {
  const {t} = useTranslation();
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title={t('marital-status')} expanded={expanded} hideDivider={true}>
    {maritalStatus.map((title) => (
        <FilterCheckboxItem key={title} title={title} />
      ))}
    </FilterGroup>
  );
};

export default FGMaritalStatus;