import { Box, Grid, Typography } from '@material-ui/core'
import { RouteComponentProps } from '@reach/router'
import React, { ReactElement } from 'react'
import { useStoreState } from '../../helpers/StoreTypedHooks'
import NiceBox from '../common/NiceBox'
import Worker2CardWrapper from '../worker/two-years/Worker2CardWrapper'

interface Props extends RouteComponentProps {
}

export default function MyFavoritesPage(props: Props): ReactElement {
  const favorites = useStoreState(s => s.favTwoYear.items);
  return (
    <Box>
      <Box py={2}>
        <Typography variant="h4">My Favorites</Typography>
      </Box>
      <NiceBox>
        <Grid container spacing={1}>
          {favorites.map((worker) => (
            // <RequestGridCard request={worker} key={worker._id} />
            <Worker2CardWrapper worker={worker} design="grid" />
          ))}
          {favorites.length === 0 && (
            <Box p={2}>
              You don't have any workers added to your favorites.
            </Box>
          )}
        </Grid>
      </NiceBox>
    </Box>
  )
}
