import axios from "axios";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";

export const apiGetLogout = async () => {
  console.log("Logout");
  const r = await axios.get<IResponse>(
    process.env.REACT_APP_API_ROOT + "/auth/logout",
    { withCredentials: true }
  );
  return r.data.success;
};
