import { Box, Button } from "@material-ui/core";
import { Link, RouteComponentProps } from "@reach/router";
import { ReactElement} from "react";

import NiceBox from "../common/NiceBox";
import WizardDatePicker from "./controls/WizardDatePicker";
import WizardMapInputField from "./controls/WizardMapInputField";
import WizardMonthMultiplePicker from "./controls/WizardMonthMultiplePicker";

export type tWorkerBy = "hour" | "month" | "day" | "";

interface Params {
  period: tWorkerBy;
}

interface Props extends RouteComponentProps<Params> {}

export default function WizardStartRental(props: Props): ReactElement {
  // const selectedWorkerBy = (by: tWorkerBy) => {
  //   setWorkerBy(by);
  // };

  // const [workerBy, setWorkerBy] = useState<tWorkerBy>("hour");
  const workerBy:tWorkerBy = props.period ? props.period : "hour";

  const goLink = workerBy === "hour" ? "/agencies/leased-hourly" : "/agencies/leased-monthly";

  return (
    <Box maxWidth={400} mx="auto">
      <NiceBox>
        <Box pb={3}>
          <WizardMapInputField />
        </Box>
        {/* <WizardRentalByField
          onChange={selectedWorkerBy}
          defaultValue={workerBy}
        /> */}
        {(workerBy === "hour" || workerBy === "day") && (
          <Box py={1}>
            <WizardDatePicker />
          </Box>
        )}
        {workerBy === "month" && (
          <Box py={1}>
            <WizardMonthMultiplePicker />
          </Box>
        )}
        <Box pt={2}>
          <Link to={goLink} style={{ textDecoration: "none" }}>
            <Button variant="contained" color="primary" fullWidth={true}>
              GO
            </Button>
          </Link>
        </Box>
      </NiceBox>
    </Box>
  );
}
