import { Box, Button } from "@material-ui/core";
import { navigate } from "@reach/router";
import React, { ReactElement } from "react";
import NiceBox from "./NiceBox";

interface Props {
  message: string;
}

export default function ErrorMessage(props: Props): ReactElement {
  const click = () => {
    navigate(-1);
  };
  const dev = process.env.NODE_ENV === "development";
  return (
    <NiceBox>
      An unexpected error has ocurred{dev ? ": " + props.message : "."}
      <Box>
        <Button onClick={click} variant="contained" color="primary">
          Back
        </Button>
      </Box>
    </NiceBox>
  );
}
