import axios, { AxiosResponse } from "axios";
import { IApiReqUpdateFavoritesTwoYear } from "estreeh-common-library/src/interfaces/api/req/IApiReqUpdateFavoritesTwoYear";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";

export const apiPutFavTwoYear = async (workerIds: string[]) => {
  console.log(`Updating user's favorites (${workerIds.length}) total`);
  const r = await axios.put<
    IApiReqUpdateFavoritesTwoYear,
    AxiosResponse<IResponse>
  >(
    process.env.REACT_APP_API_ROOT + "/customer/favorites/two-year",
    {
      workerIds,
    },
    { withCredentials: true }
  );
  console.log("Favorite update completed");
  return r.data.data;
};
