import { Badge, Box, Typography } from "@material-ui/core";
import { navigate } from "@reach/router";
import React, { ReactElement } from "react";
import Branding from "../../config/Branding";
import { ITopNavItem } from "estreeh-common-library/src/interfaces/ITopNavItem";

interface Props {
  item: ITopNavItem;
  setIsOpen: Function;
}

export default function BurgerItem(props: Props): ReactElement {
  const item = props.item;
  // const onClick =
  //   props.item.onClick !== undefined
  //     ? props.item.onClick
  //     : () => {
  //         if (props.item.to) navigate(props.item.to);
  //         props.setIsOpen(false);
  //       };
  const onClick = () => {
    if (props.item.onClick !== undefined) {
      props.item.onClick();
    } else {
      if (props.item.to) navigate(props.item.to);
    }
    props.setIsOpen(false);
  };
  return (
    <Typography>
      <Box
        py={1.5}
        onClick={onClick}
        css={{
          color: Branding.text.primary,
          // border: "2px solid #000",
          // borderColor: item.prominent ? "#000" : "#00000000",
          fontWeight: 600,
          cursor: "pointer",
          borderRadius: Branding.borderRadius.niceRadius,
          transition: Branding.transition,
        }}
      >
        <Box>
          <Badge badgeContent={item.count} color="primary">
          {item.title}
          {item.node}
          </Badge>
        </Box>
      </Box>
    </Typography>
  );
}
