import { Box, Grid, GridSize } from "@material-ui/core";
import React, { ReactNode } from "react";
import IHourlySlot from "estreeh-common-library/src/interfaces/IHourlySlot";
import _ from "lodash";
import HourlySlotsDay from "./HourlySlotsDay";

const WorkerRentalCardHourlySlots = (props: {
  slotsDictionary: _.Dictionary<IHourlySlot[]>;
  md: GridSize;
  maxDays: number;
}) => {
  // const slotsDictionary = _.cloneDeep(props.slotsDictionary);

  // Object.keys(slotsDictionary).forEach((key, index) => {
  //   if (index >= props.maxDays) delete slotsDictionary[key];
  // });

  // const items = _.map(slotsDictionary, (slots, key) => (
  //   <HourlySlotsDay slots={slots} dateKey={key} md={props.md} />
  // ));

  let items: ReactNode[] = [];
  const slotsDictionary = props.slotsDictionary;

  Object.keys(slotsDictionary).forEach((key, index) => {
    if (index < props.maxDays) {
      items.push(
        <HourlySlotsDay
          slots={slotsDictionary[key]}
          dateKey={key}
          md={props.md}
        />
      );
    }
  });

  return (
    <Box>
      <Grid container>{items}</Grid>
    </Box>
  );
};

export default WorkerRentalCardHourlySlots;
