import { Box, Link, Typography } from "@material-ui/core";
import { CSSProperties } from "@material-ui/core/styles/withStyles";
import axios from "axios";
import { useState } from "react";
import { IApiResCheckMobile } from "estreeh-common-library/src/interfaces/api/res/IApiResCheckMobile";
import { IApiResSendOtp } from "estreeh-common-library/src/interfaces/api/res/IApiResSendOtp";
import { IApiResValidateOtp } from "estreeh-common-library/src/interfaces/api/res/IApiResValidateOtp";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import NiceBox from "../common/NiceBox";
import LoginEnterMobile from "./LoginEnterMobile";
import RegisterCustomer from "./RegisterCustomer";
import { toast } from "react-toastify";
import LoginFormFields from "./LoginFormFields";
import { IApiReqSendOtp } from "estreeh-common-library/src/interfaces/api/req/IApiReqSendOtp";
import { IApiReqValidateOtp } from "estreeh-common-library/src/interfaces/api/req/IApiReqValidateOtp";

interface Props {
  onLogin: Function;
  setIntent: React.Dispatch<React.SetStateAction<"login" | "signUp">>;
}

export const RegisterStart = (props: Props) => {
  const boxStyle: CSSProperties = {
    textAlign: "center",
  };

  const [loading, setLoading] = useState(false);
  const [errored, setErrored] = useState(false);
  const [newUser, setNewUser] = useState<boolean | undefined>(undefined);
  const [mobile, setMobile] = useState("");
  const [secret, setSecret] = useState("");
  const [checkMobileResult, setCheckMobileResult] = useState<
    IApiResCheckMobile | undefined
  >(undefined);

  const [allowResend, setAllowResend] = useState(new Date());
  const [otpSent, setOtpSent] = useState(false);
  const [otpValidated, setOtpValidated] = useState(false);

  function sendOtp() {
    setLoading(true);
    const sendOtpBody: IApiReqSendOtp = {
      to: `+973${mobile}`,
      method: "sms",
      purpose: "newUser",
    };
    axios
      .post<IResponse<IApiResSendOtp>>(
        process.env.REACT_APP_API_ROOT + "/otp/send",
        sendOtpBody
      )
      .then((result) => {
        setAllowResend(new Date(result.data.data.allowResend));
        toast.success(result.data.message);
        setOtpSent(true);
      })
      .catch((reason) => {
        toast.error(reason.response.data.message);
      })
      .finally(() => {
        setLoading(false);
      });
  }

  function validateOtp() {
    setLoading(true);
    const validateOtpBody:IApiReqValidateOtp = {
      to: `+973${mobile}`,
      secret,
    }
    axios
      .post<IResponse<IApiResValidateOtp>>(
        process.env.REACT_APP_API_ROOT + "/otp/validate",
        validateOtpBody
      )
      .then((result) => {
        toast.success(result.data.message);
        setOtpValidated(result.data.success);
      })
      .catch((reason) => {
        toast.error(reason.response.data.message);
        setSecret("");
      })
      .finally(() => {
        setLoading(false);
      });
  }

  const childProps = {
    loading,
    errored,
    mobile,
    newUser,
    otpSent,
    checkMobileResult,
    allowResend,
    secret,
    setSecret,
    setMobile,
    setNewUser,
    setLoading,
    setErrored,
    setOtpSent,
    setCheckMobileResult,
    sendOtp,
    validateOtp,
    onLogin: props.onLogin,
    setIntent: props.setIntent,
  };

  return (
    <Box maxWidth={960} mx="auto" mt={7} mb={12}>
      <NiceBox style={boxStyle} p={6}>
        {newUser !== false && otpValidated === false && (
          <LoginEnterMobile {...childProps} />
        )}
        {otpValidated === true && <RegisterCustomer {...childProps} />}
        {newUser === false && (
          <Box>
            <Box mb={2} textAlign="center">
              <Typography variant="h5">Login</Typography>
            </Box>
            <Box my={2}>
              An existing account was found for mobile {mobile}, please login to
              continue:
            </Box>

            <LoginFormFields onLogin={props.onLogin} mobile={mobile} />
            <Box>
              Wrong mobile number?
              <br />
              <Link
                onClick={function () {
                  setMobile("");
                  setNewUser(undefined);
                }}
                style={{ cursor: "pointer" }}
              >
                Click to enter another mobile
              </Link>
            </Box>
          </Box>
        )}
      </NiceBox>
    </Box>
  );
};
