import { InputAdornment } from "@material-ui/core";
import React, { ReactElement } from "react";
import CountryFlag from "../common/CountryFlag";

export default function MobileAdornment(): ReactElement {
  return (
    <InputAdornment position="start">
      <>
      <CountryFlag
        country={{
          country: "Bahrain",
          countryCode: "BH",
          titleTranslated: "",
          titleTranslatedWithFees: "Bahrain",
        }}
        marginRight={8}
        showFees={false}
      />
      <span>+973</span>
      </>
    </InputAdornment>
  );
}
