import { Box, Button, TextField } from "@material-ui/core";
import { FormikProps } from "formik";
import React, { ReactElement } from "react";
import { IApiReqPasswordReset } from "estreeh-common-library/src/interfaces/api/req/IApiReqPasswordReset";

interface Props {
  formik: FormikProps<IApiReqPasswordReset | any>;
}

export default function PasswordResetForm({ formik }: Props): ReactElement {
  return (
    <form onSubmit={formik.handleSubmit} autoComplete="off">
      <Box my={2}>
        <Box my={2}>
          Please enter the OTP sent to: <strong>{formik.values.mobile}</strong>
        </Box>
        <TextField
          variant="outlined"
          label="OTP"
          autoComplete="new-password"
          error={formik.touched.secret && formik.errors.secret !== undefined}
          helperText={formik.touched.secret && formik.errors.secret}
          {...formik.getFieldProps("secret")}
        />
        <TextField
          variant="outlined"
          label="New Password"
          type="password"
          autoComplete="new-password"
          error={
            formik.touched.password && formik.errors.password !== undefined
          }
          helperText={formik.touched.password && formik.errors.password}
          {...formik.getFieldProps("password")}
        />
        <TextField
          variant="outlined"
          label="Confirm Password"
          type="password"
          autoComplete="new-password"
          error={
            formik.touched.confirmPassword &&
            formik.errors.confirmPassword !== undefined
          }
          helperText={
            formik.touched.confirmPassword && formik.errors.confirmPassword
          }
          {...formik.getFieldProps("confirmPassword")}
        />
      </Box>
      <Box textAlign="center">
        <Button
          type="submit"
          color="primary"
          variant="contained"
          disabled={formik.isSubmitting}
        >
          Reset Password
        </Button>
      </Box>
    </form>
  );
}
