import { Box } from "@material-ui/core";
import React, { ReactElement } from "react";
import IWorkerRental from "estreeh-common-library/src/interfaces/IWorkerRental";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import RequestWorkerRental from "./RequestWorkerRental";
import RequestWorkerTwoYear from "./RequestWorkerTwoYear";

interface Props {
  workerTwoYear?: IWorkerTwoYear;
  workerRental?: IWorkerRental;
}

export default function RequestWorkerContainer(props: Props): ReactElement {
  const picWidth = "100%";
  const picHeight = 180;

  return (
    <Box>
      {props.workerTwoYear !== undefined && (
        <RequestWorkerTwoYear
          worker={props.workerTwoYear}
          picHeight={picHeight}
          picWidth={picWidth}
        />
      )}
      {props.workerRental !== undefined && (
        <RequestWorkerRental
          worker={props.workerRental}
          picHeight={picHeight}
          picWidth={picWidth}
        />
      )}
    </Box>
  );
}
