export default function TimeToDate(timeString: string): Date {
  let now = new Date();
  let today: string =
    now.getFullYear() + "/" + (now.getMonth() + 1) + "/" + now.getDate();
  let dateString = today + " " + timeString;
  return new Date(dateString);
}

export function AddToTime(time: string, hours: number): string {
  let date = TimeToDate(time);
  AddHoursToDate(date, hours);
  let h = (date.getHours() < 10 ? "0" : "") + date.getHours();
  let m = (date.getMinutes() < 10 ? "0" : "") + date.getMinutes();
  let updatedTime: string = h + ":" + m;
  return updatedTime;
}

export function AddHoursToDate(date: Date, hours: number) {
  date.setTime(date.getTime() + hours * 60 * 60 * 1000);
  return date;
}
