import { Box, Typography } from "@material-ui/core";
import React, { ReactElement } from "react";
import { IApiResNewRequest } from "estreeh-common-library/src/interfaces/api/res/IApiResNewRequest";
import Branding from "../../config/Branding";

interface Props {
  response?: IApiResNewRequest;
}

export default function TrackingCode(props: Props): ReactElement {
  return (
    <Box my={3}>
      <Typography variant="h4">Tracking ID: </Typography>
      <Typography variant="h5" style={{ color: Branding.primary, fontWeight: "bold" }}>
        {props.response?.trackingCode}
      </Typography>
    </Box>
  );
}
