import { Box, Button, Grid, TextField } from "@material-ui/core";
import { CSSProperties } from "@material-ui/styles";
import { Link } from "@reach/router";
import axios from "axios";
import React, { ReactElement, useState } from "react";
import { toast } from "react-toastify";
import { IApiReqLogin } from "estreeh-common-library/src/interfaces/api/req/IApiReqLogin";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { useStoreActions } from "../../helpers/StoreTypedHooks";
import SmallText from "../common/SmallText";
import MobileInput from "./MobileInput";

interface Props {
  demoLink?: string;
  onLogin: Function;
  mobile?: string;
}

export default function LoginFormFields(props: Props): ReactElement {
  const demoLink = props.demoLink ? props.demoLink : "";

  const [password, setPassword] = useState("");
  const [mobile, setMobile] = useState(props.mobile !== undefined ? props.mobile : "");
  const [gotError, setGotError] = useState(false);
  const [inProgress, setInProgress] = useState(false);
  const [attempts, setAttempts] = useState(0);
  const loadUser = useStoreActions((action) => action.loadUser);
  // const returnUrl = useStoreState((state) => state.returnUrl);
  // const setReturnUrl = useStoreActions((action) => action.returnUrl.setItem);

  
  const textBoxStyle: CSSProperties = {
    backgroundColor: gotError ? "#ffeeee" : "#ffffff",
  };

  const loginButtonDisabled =
    inProgress || password.length < 1 || mobile.length !== 8;

  function onLoginButtonClick() {
    if (!loginButtonDisabled) {
      setInProgress(true);
      const postBody: IApiReqLogin = {
        mobile: `+973${mobile}`,
        password,
        app:"customer-fe"
      };
      axios
        .post<IResponse>(
          process.env.REACT_APP_API_ROOT + "/auth/login",
          postBody,
          { withCredentials: true }
        )
        .then((response) => {
          if (response.data.success) {
            loadUser().finally(() => {
              setGotError(false);
              
              // navigate(newReturnUrl);
              // navigate(-1);
              props.onLogin();
            });
          } else {
            loginFailed();
          }
        })
        .catch((reason) => {
          loginFailed();
        })
        .finally(() => {
          setInProgress(false);
        });
    }
  }

  function onTextBoxKeyPress(e: React.KeyboardEvent) {
    if (e.key === "Enter") {
      onLoginButtonClick();
    }
    setGotError(false);
  }

   

  function loginFailed(){
    //TODO: Use dialog (or better UI) instead of snackbar
    const a = attempts + 1;
    setAttempts((prev) => a);
    setGotError(true);
    const extraMsg = a > 1 ? ` (Attempt #${a}).` : `.`;
    const msg = "Wrong username or password" + extraMsg;
    console.log(msg);
    setPassword("");
    toast.error(msg);
  };

  return (
    <Box maxWidth={346} mx="auto">
      <Link to={demoLink} style={{ textDecoration: "none" }}>
        {/* <SignInWithGoogle />
              <SignInWithApple /> */}
      </Link>
      {/* <Box textAlign="center" px={1}>
            <Typography>OR</Typography>
          </Box> */}
      <Box py={3}>
        <form noValidate autoComplete="off">
          <MobileInput
            value={mobile}
            onChange={(event) => {
              setMobile(event.target.value);
            }}
            isErrored={gotError}
            onKey={onTextBoxKeyPress}
            disabled={inProgress}
          />
          <TextField
            label="Password"
            type="password"
            autoComplete="current-password"
            variant="outlined"
            value={password}
            onChange={(event) => {
              setPassword(event.target.value);
            }}
            style={textBoxStyle}
            onKeyPress={onTextBoxKeyPress}
            disabled={inProgress}
          />
          <Box py={1}>
            <Grid container alignItems="center">
              <Grid item xs={8}>
                {/* <Box>
                        <FormControlLabel
                          control={<Checkbox color="primary" size="small" />}
                          label={<SmallText>Keep Me Logged In</SmallText>}
                        />
                      </Box> */}
              </Grid>
              <Grid item xs={4}>
                <Box textAlign="right" py={0}>
                  <SmallText>
                    <Link to="/forgot-password">Forgot password?</Link>
                  </SmallText>
                </Box>
              </Grid>
            </Grid>
          </Box>
        </form>
        <Box py={1}>
          <Button
            color="primary"
            variant="contained"
            fullWidth={true}
            onClick={onLoginButtonClick}
            disabled={loginButtonDisabled}
          >
            {inProgress ? "Please Wait..." : "LOGIN"}
          </Button>
        </Box>
      </Box>
    </Box>
  );
}
