/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import { Badge, Box } from "@material-ui/core";
import React, { ReactElement } from "react";
import Branding from "../../config/Branding";
import { ITopNavItem } from "estreeh-common-library/src/interfaces/ITopNavItem";
import { navigate } from "@reach/router";

interface Props {
  item: ITopNavItem;
}

export default function TopMenuItem(props: Props): ReactElement {
  const item = props.item;
  const onClick =
    item.onClick !== undefined
      ? item.onClick
      : () => {
          if (item.to) navigate(item.to);
        };
  return (
    <Box>
      <Badge badgeContent={item.count} color="primary" overlap={item.badgeOverlap}>
        <Box
          onClick={onClick}
          py={0.5}
          px={item.prominent || item.node ? 3 : 2}
          textAlign="center"
          mx={0.25}
          css={{
            border: "2px solid #000",
            borderColor: item.prominent ? "#000" : "#00000000",
            fontWeight: 600,
            fontSize: "90%",
            cursor: "pointer",
            borderRadius: Branding.borderRadius.niceRadius,
            transition: Branding.transition,
            // backgroundColor:Branding.lightest,
            ":hover": {
              color: "white",
              backgroundColor: Branding.primary,
              boxShadow: "0 0 4px " + Branding.light,
            },
          }}
        >
          <Box>
            {props.item.title}
            {props.item.node}
          </Box>
        </Box>
      </Badge>
    </Box>
  );
}
