import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@material-ui/core";
import { navigate } from "@reach/router";
import React, { ReactElement } from "react";
import { toast } from "react-toastify";
import { apiGetLogout } from "../../api-calls/ApiGetLogout";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";

interface Props {
  open: boolean;
  setOpen: React.Dispatch<React.SetStateAction<boolean>>;
}

export default function LogoutDialog(props: Props): ReactElement {
  const setUser = useStoreActions((a) => a.user.setItem);
  const setFavorites = useStoreActions(a => a.favTwoYear.setItems);

  const logoutConfirmed = () => {
    apiGetLogout().finally(() => {
      setUser(undefined);
      setFavorites({
        payload: []
      });
      handleClose();
      toast.info("Logged out successfully.");
      navigate("/");
    });
  };

  const handleClose = () => {
    props.setOpen(false);
  };

  const user = useStoreState((s) => s.user.item);

  return (
    <Dialog open={props.open} onClose={handleClose}>
      <DialogTitle>Logout</DialogTitle>
      <DialogContent>
        <DialogContentText>
          {user?.fullName}, Are you sure you want to logout?
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} variant="outlined" style={{ color: "#aaa" }}>
          Cancel
        </Button>
        <Button onClick={logoutConfirmed} variant="outlined" color="primary" autoFocus>
          Logout
        </Button>
      </DialogActions>
    </Dialog>
  );
}
