import { Button } from "@material-ui/core";
// import { Link } from "@reach/router";
import React from "react";
import { useTranslation } from "react-i18next";

const SelectWorkerButton = (props: {
  onClick: React.MouseEventHandler<HTMLButtonElement>;
}) => {
  const { t } = useTranslation();
  // const linkStyles: React.CSSProperties = {
  //   display: "inline-flex",
  //   textDecoration: "none",
  // };

  // const loggedIn = props.toolboxData.user !== undefined;
  // const url = loggedIn ? "/new-request/two-year-worker" : "/login-prompt";

  return (
    <React.Fragment>
      <Button
        style={{ marginLeft: 8 }}
        size="small"
        color="primary"
        variant="outlined"
        onClick={props.onClick}
      >
        {t("button.select")}
      </Button>
    </React.Fragment>
  );
};

export default SelectWorkerButton;
