import { Box, Grid, Typography } from "@material-ui/core";
import React, { ReactElement } from "react";
import IHourlySlot from "estreeh-common-library/src/interfaces/IHourlySlot";
import HourlySlotMiniDisplay from "./HourlySlotMiniDisplay";
import dateFormat from "dateformat";
import Branding from "../../../config/Branding";
import {  NavigateBefore, NavigateNext } from "@material-ui/icons";

const HourlySlotsDayMiniDisplay = (props: {
  slots?: IHourlySlot[];
  leftOnClick: any;
  rightOnClick: any;
  showLeftArrow: boolean;
  showRightArrow: boolean;
  date: Date;
}): ReactElement => {
  const date = props.date;

  const day = dateFormat(date, "ddd");
  const dayMonth = dateFormat(date, "d-mmm");
  const iconSize = 28;

  const textStyle: React.CSSProperties = {
    fontSize: Branding.fontSize.small - 2,
    textAlign: "center",
    fontWeight: "bold",
    color: Branding.text.primary,
  };

  const iconStyle: React.CSSProperties = {
    fontSize: iconSize,
    verticalAlign: "middle",
    cursor: "pointer",
    color: Branding.primary,
  };

  let items = null;

  if (props.slots && props.slots.length > 0) {
    items = props.slots.map((slot) => (
      <Grid item xs={4}>
        <HourlySlotMiniDisplay slot={slot} />
      </Grid>
    ));
  }

  return (
    <Box>
      <Box py={0}>
        <Typography style={textStyle}>
          <Grid container>
            <Grid item xs={3}>
              {props.showLeftArrow && (
                <Box onClick={props.leftOnClick} textAlign="right">
                  <NavigateBefore fontSize="small" style={iconStyle} />
                </Box>
              )}
            </Grid>
            <Grid item xs={6}>
              <Box lineHeight={iconSize + "px"}>
                {day}&nbsp;{dayMonth}
              </Box>
            </Grid>
            <Grid item xs={3}>
              {props.showRightArrow && (
                <Box onClick={props.rightOnClick} textAlign="left">
                  <NavigateNext fontSize="small" style={iconStyle} />
                </Box>
              )}
            </Grid>
          </Grid>
        </Typography>
      </Box>
      <Box>
        <Grid container>{items}</Grid>
      </Box>
    </Box>
  );
};

export default HourlySlotsDayMiniDisplay;
