import { Box, Typography } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import { Formik } from "formik";
import React, { ReactElement } from "react";
import NiceBox from "../common/NiceBox";
import MyProfileForm from "./MyProfileForm";
import * as Yup from "yup";
import axios from "axios";
import { toast } from "react-toastify";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { IApiReqUpdateMyProfile } from "estreeh-common-library/src/interfaces/api/req/IApiReqUpdateMyProfile";
import { useStoreState } from "../../helpers/StoreTypedHooks";

interface Props extends RouteComponentProps {}

export default function MyProfilePage(props: Props): ReactElement {
  const user = useStoreState((s) => s.user.item);
  const initialAppForm: IApiReqUpdateMyProfile = {
    fullName: user?.fullName,
    email: user?.email,
    mobile2: user?.mobile2,
  };

  return (
    <Box>
      <Box py={2}>
        <Typography variant="h4">Profile</Typography>
      </Box>
      <NiceBox>
        <Formik
          validationSchema={Yup.object({
            fullName: Yup.string().required("Required").min(3, "Minimum 3 characters"),
            // email: Yup.string().email(),
          })}
          initialValues={initialAppForm}
          onSubmit={(values, { setSubmitting }) => {
            setSubmitting(true);
            axios
              .put<IResponse<boolean>>(
                process.env.REACT_APP_API_ROOT + "/auth/me",
                values,
                { withCredentials: true }
              )
              .then((result) => {
                toast.success(result.data.message);
              })
              .catch((reason) => {
                toast.error(reason.response.data.message);
              })
              .finally(() => {
                setSubmitting(false);
              });
          }}
        >
          {(formik) => <MyProfileForm formik={formik} mobile={user?.mobile} />}
        </Formik>
      </NiceBox>
    </Box>
  );
}
