import { Drawer } from "@material-ui/core";
import React from "react";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import { Anchor } from "estreeh-common-library/src/types/Anchor";
import { Design } from "estreeh-common-library/src/types/Design";
import Worker2Details from "./Worker2Details";
import { withBreakpoints } from "react-breakpoints";
import { useNavigate } from "@reach/router";
import Worker2ListCard from "./Worker2ListCard";
import Worker2GridCard from "./Worker2GridCard";
import { ClickEvent } from "estreeh-common-library/src/types/ClickEvent";
import {
  useStoreActions,
  useStoreState,
} from "../../../helpers/StoreTypedHooks";

const Worker2CardWrapper = (props: {
  worker: IWorkerTwoYear;
  design: Design;
  breakpoints: any;
  currentBreakpoint: any;
}) => {
  const user = useStoreState((s) => s.user.item);
  const setReturnUrl = useStoreActions((a) => a.returnUrl.setItem);
  // const setSelectedWorker = useStoreActions(
  //   (a) => a.selectedWorkerTwoYear.setItem
  // );

  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const nextUrl = "/new-request/two-year-worker/" + props.worker.guid;
  const loggedIn = user !== undefined;

  const navigate = useNavigate();

  const toggleDrawer =
    (anchor: Anchor, open: boolean) =>
    (event: React.KeyboardEvent | React.MouseEvent) => {
      if (
        event.type === "keydown" &&
        ((event as React.KeyboardEvent).key === "Tab" ||
          (event as React.KeyboardEvent).key === "Shift")
      ) {
        return;
      }

      setState({ ...state, [anchor]: open });
    };

  const linkToDrawerPage =
    () => (event: React.KeyboardEvent | React.MouseEvent) => {
      navigate(
        props.worker.agency?._id + "/worker-details/" + props.worker._id
      );
    };

  const { breakpoints, currentBreakpoint } = props;

  const anchor: Anchor = "right";

  const onReserveButton = () => {
    // setSelectedWorker(props.worker);
    setReturnUrl(nextUrl);
    navigate(loggedIn ? nextUrl : "/login-prompt");
  };

  let picClickEvent: ClickEvent;

  const isMd = breakpoints[currentBreakpoint] > breakpoints.md;

  if (isMd) {
    picClickEvent = toggleDrawer(anchor, true);
  } else {
    picClickEvent = linkToDrawerPage();
  }

  return (
    <React.Fragment key={props.worker._id}>
      {props.design === "list" && (
        <Worker2ListCard
          worker={props.worker}
          picClickEvent={picClickEvent}
          onReserveButton={onReserveButton}
        />
      )}
      {props.design === "grid" && (
        <Worker2GridCard
          worker={props.worker}
          picClickEvent={picClickEvent}
          onReserveButton={onReserveButton}
        />
      )}
      {isMd && (
        <Drawer
          anchor={anchor}
          open={state[anchor]}
          onClose={toggleDrawer(anchor, false)}
        >
          <Worker2Details
            worker={props.worker}
            onReserveButton={onReserveButton}
          />
        </Drawer>
      )}
    </React.Fragment>
  );
};

export default withBreakpoints(Worker2CardWrapper);
