import * as Factory from "factory.ts";
import Faker from "faker";
import IHourlySlot from "estreeh-common-library/src/interfaces/IHourlySlot";

const HourlySlotFactory = Factory.Sync.makeFactory<IHourlySlot>({
  id: Factory.each((i) => i + 1),
  availabilityStatus: Factory.each(() =>
    Faker.random.arrayElement([
      "Free",
      "Free",
      "Free",
      "Free",
      "Booked",
      "Booked",
      // "Off",
    ])
  ),
  availabilityDisplayText: "",
  color: "",
  date: new Date(),
  dayDisplay: "",
  dateDisplay: "",
  dateFull: "",
  clickable: false,
  selected: false,
});

export default HourlySlotFactory;
