import React from 'react';
import { useTranslation } from 'react-i18next';
import { maxAge, minAge } from '../../config/FilterOptions';
import { IFilterGroupContainer } from 'estreeh-common-library/src/interfaces/IFilterGroupContainer';
import FilterGroup from './FilterGroup';
import FilterSlider from './FilterSlider';

const FGAge = (props:IFilterGroupContainer) => {
  const {t} = useTranslation();
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title={t("age")} expanded={expanded}>
    <FilterSlider
        defaultValue={[20, 45]}
        min={minAge}
        max={maxAge}
        step={1}
        isMd={props.isMd}
      />
    </FilterGroup>
  );
};

export default FGAge;