import { Box, Button, TextField } from '@material-ui/core'
import { FormikProps } from 'formik'
import React, { ReactElement } from 'react'
import { IApiReqUpdateMyProfile } from 'estreeh-common-library/src/interfaces/api/req/IApiReqUpdateMyProfile'

interface Props {
  formik: FormikProps<IApiReqUpdateMyProfile>
}

export default function ChangePasswordForm({formik}: Props): ReactElement {
  return (
    <form onSubmit={formik.handleSubmit} autoComplete="off">
    <Box my={2}>
    <TextField
          variant="outlined"
          label="New Password"
          type="password"
          autoComplete="new-password" 
          error={formik.touched.password && formik.errors.password !== undefined}
          helperText={formik.touched.password && formik.errors.password}
          {...formik.getFieldProps("password")}
        />
        <TextField
          variant="outlined"
          label="Confirm Password"
          type="password"
          autoComplete="new-password"
          error={formik.touched.confirmPassword && formik.errors.confirmPassword !== undefined}
          helperText={formik.touched.confirmPassword && formik.errors.confirmPassword}
          {...formik.getFieldProps("confirmPassword")}
        />
    </Box>
    <Button
      type="submit"
      color="primary"
      variant="contained"
      disabled={formik.isSubmitting}
    >
      Save
    </Button>
  </form>
  )
}
