import { Button } from "@material-ui/core";
import React, { ReactElement, useEffect, useState } from "react";
import Branding from "../../config/Branding";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";
import NiceBox from "./NiceBox";

interface Props {}

function timeout(delay: number) {
  return new Promise((res) => setTimeout(res, delay));
}

export default function FilteredResultsAlert(props: Props): ReactElement {
  const filtersCount = useStoreState(
    (s) => s.filterTwoYear.items.length + s.filterRental.items.length
  );
  const clearFilters = useStoreActions((a) => a.clearFilters);
  const [bgColor, setBgColor] = useState("#ffffaa");
  const [boxShadow, setBoxShadow] = useState("");
  const [transition, setTransition] = useState(Branding.transitionSlow);

  useEffect(() => {
    const flashReset = async () => {
      setTransition("0");
      setBoxShadow("0 0 6px #ff000033");
      setBgColor("#ffffaa");
    };

    const flashColor = async () => {
      await flashReset();
      await timeout(100);
      setTransition(Branding.transitionSlow);
      setBoxShadow(`0 0 9px ${Branding.primary}66`);
      setBgColor(Branding.lightest);
    };

    if (filtersCount === 0) flashReset();
    else flashColor();
  }, [filtersCount]);

  if (filtersCount === 0) {
    return <></>;
  }

  return (
    <NiceBox
      style={{
        backgroundColor: bgColor,
        boxShadow: boxShadow,
        transition: transition,
      }}
      p={3}
    >
      <span style={{ marginRight: 16 }}>You are viewing filtered results</span>
      <Button
        color="primary"
        size="small"
        variant="contained"
        onClick={() => clearFilters()}
      >
        Clear Filters
      </Button>
    </NiceBox>
  );
}
