import { Icon } from "@material-ui/core";
import React from "react";
import { useTranslation } from "react-i18next";

const RatingHtml = (props: { rating: number }) => {
  const {t} = useTranslation();
  const rating = props.rating;
  const iconSize = 15;

  if (rating >= 4.25) return <React.Fragment>😃 {t("rating.amazing")}</React.Fragment>;
  if (rating >= 3.5) return <React.Fragment>🙂 {t("rating.good")}</React.Fragment>;
  if (rating >= 3.5) return <React.Fragment><Icon style={{ fontSize:iconSize }}>sentiment_satisfied_sharp</Icon> Good!</React.Fragment>;
  if (rating >= 3) return <React.Fragment>😐 {t("rating.ok")}</React.Fragment>;
  if (rating >= 3) return <React.Fragment><Icon style={{ fontSize:iconSize }}>sentiment_satisfied</Icon> Ok</React.Fragment>;
  return <React.Fragment>{t("rating.not-yet-rated")}</React.Fragment>;
};

export default RatingHtml;
