import { navigate, RouteComponentProps } from "@reach/router";
import React from "react";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import ScrollToTop from "../common/ScrollToTop";
import Worker2Details from "../worker/two-years/Worker2Details";
import { ObjectId } from "bson";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";

interface Params {
  workerTwoYearId: ObjectId;
  workersTwoYear: IWorkerTwoYear[];
  onReserveButton: React.MouseEventHandler<HTMLButtonElement>;
}

interface Props extends RouteComponentProps<Params> {}

const WorkerDrawerPage = (props: Props) => {
  const workersTwoYear = props.workersTwoYear ? props.workersTwoYear : [];
  const workerId = props.workerTwoYearId ? props.workerTwoYearId : undefined;
  const worker = workersTwoYear.find((worker) => worker._id === workerId);
  const user = useStoreState((state) => state.user.item);
  const loggedIn = user !== undefined;
  const nextUrl = "/new-request/two-year-worker/" + worker?.guid;
  const setReturnUrl = useStoreActions((action) => action.returnUrl.setItem);
  // const setSelectedWorker = useStoreActions(
  //   (action) => action.selectedWorkerTwoYear.setItem
  // );

  let onReserveButton: React.MouseEventHandler<HTMLButtonElement> = () => {};

  onReserveButton = props.onReserveButton
    ? props.onReserveButton
    : () => {
        setReturnUrl(nextUrl);
        // setSelectedWorker(worker);
        navigate(loggedIn ? nextUrl : "/login-prompt");
      };

  return (
    <>
      <ScrollToTop />
      {worker && (
        <Worker2Details worker={worker} onReserveButton={onReserveButton} />
      )}
    </>
  );
};

export default WorkerDrawerPage;
