import { Box, Divider } from "@material-ui/core";
import React from "react";
import UseStyles from "../../../config/UseStyles";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import Worker2ListCardInfo from "./Worker2ListCardInfo";
import Worker2ListCardPicTitle from "./Worker2ListCardPicTitle";


const Worker2ListCard = (props: {
  worker: IWorkerTwoYear;
  picClickEvent: any;
  onReserveButton: React.MouseEventHandler<HTMLButtonElement>;
}) => {

  const classes = UseStyles();

  return (
    <React.Fragment key={props.worker._id}>
      <Box pt={1} className={classes.hoverBox}>
        <Worker2ListCardPicTitle
          onPicTitleClick={props.picClickEvent}
          worker={props.worker}
          showButtons={true}
          onReserveButton={props.onReserveButton}
        >
          <Worker2ListCardInfo worker={props.worker} />
        </Worker2ListCardPicTitle>
        <Box pt={1}>
          <Divider />
        </Box>
      </Box>
 
    </React.Fragment>
  );
};

export default Worker2ListCard;
