import { Box, Container, Grid } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React from "react";
import Branding from "../../config/Branding";
import Logo from "../common/Logo";
import TopMenu from "./TopMenu";


interface Params {
  isMd: boolean;
  burgerWidth:number;
}

interface Props extends RouteComponentProps<Params> {
}

const SiteHead = (props: Props) => {
  const isMd = props.isMd !== undefined ? props.isMd : true;
  const burgerWidth = props.burgerWidth ? props.burgerWidth : 1;

  return (
    <React.Fragment>
      {!isMd && <TopMenu isBurger={true} burgerWidth={burgerWidth} burgerLeft={16} burgerTop={22} />}
      <Box bgcolor={Branding.lighter}>
        <Container>
          <Box mb={0} py={2}>
            <Grid container spacing={0}>
              <Grid item xs={props.isMd ? 2 : 12}>
                <Logo marginLeft={16 + burgerWidth} maxHeight={36}/>
              </Grid>
              {props.isMd && (<Grid item xs={10}>
                <TopMenu isBurger={false} />
              </Grid>)}
            </Grid>
          </Box>
        </Container>
      </Box>
    </React.Fragment>
  );
};

export default SiteHead;
