import { Grid } from "@material-ui/core";
import { navigate } from "@reach/router";
import { ITopNavItem } from "estreeh-common-library/src/interfaces/ITopNavItem";
import React, { ReactElement } from "react";

interface Props {
  menu: ITopNavItem;
  last: boolean;
}

export default function FooterMenuItem({ menu: m, last }: Props): ReactElement {

  const grid = 8;

  const onClick =
    m.onClick !== undefined
      ? m.onClick
      : () => {
          if (m.to) navigate(m.to);
        };

  const div = !last ? <Grid item style={{ paddingRight:grid, paddingLeft:grid }}>|</Grid> : undefined;

  return (
    <>
      <Grid
        item
        style={{
          // borderRight: last ? undefined :  "1px solid #fff",
          paddingRight: grid,
          paddingLeft: grid,
          // marginRight: last ? undefined : 8,
          cursor: "pointer",
          // display: "inline",
          // "&:before":{
          //   content:"|",
          // }
        }}
        onClick={onClick}
      >
        {m.title}
        {m.node}
      </Grid>
      {div}
    </>
  );
}
