import { Box, Button, Divider, Grid, Typography } from "@material-ui/core";
import { Link, RouteComponentProps } from "@reach/router";
import React, { CSSProperties } from "react";
import Check from "../../images/check.svg";
import NiceBox from "../common/NiceBox";
import RequestWorkerContainer from "../request/RequestWorkerContainer";
import LabelValueLine from "../common/LabelValueLine";
import { navigate } from "@reach/router";
import RequestScheduleDisplay from "../request/RequestScheduleDisplay";
import TrackingCode from "../user/TrackingCode";
// import OneClickRegistration from "../user/OneClickRegistration";
import { useStoreState } from "../../helpers/StoreTypedHooks";
import ScrollToTop from "../common/ScrollToTop";

interface Params {
  message: string;
  smallMessage: string;
}

interface Props extends RouteComponentProps<Params> {}

const SuccessPage = (props: Props) => {
  const rcr = useStoreState((s) => s.recent.request.item);
  const rcrr = useStoreState((s) => s.recent.requestResponse.item);
  if (rcr === undefined || rcrr === undefined) return <></>;

  const buttonBoxStyle: CSSProperties = {
    margin: 4,
    display: "inline-flex",
  };

  const buttonSize = "medium";
  const oneLine = true;
  const applicationForm = rcr.applicationForm;

  const nextButtons = (
    <Box py={2} textAlign="center">
      <Box style={buttonBoxStyle}>
        <Button
          variant="outlined"
          color="primary"
          size={buttonSize}
          onClick={() => {
            navigate("/account/my-requests");
          }}
        >
          My Requests
        </Button>
      </Box>
      <Box style={buttonBoxStyle}>
        <Link to="/" style={{ textDecoration: "none" }}>
          <Button variant="outlined" color="primary" size={buttonSize}>
            Back to Home
          </Button>
        </Link>
      </Box>
    </Box>
  );

  return (
    <>
      <ScrollToTop />
      <NiceBox p={4}>
        <Box p={1} maxWidth={600} mx="auto">
          <Box textAlign="center">
            <Box maxWidth={60} mx="auto" pb={2}>
              <img src={Check} style={{ display: "block" }} alt="success" />
            </Box>
            <Typography variant="h5">Success!</Typography>
            <Box py={1}>
              <Typography variant="h6">{props.message}</Typography>
            </Box>
            <Box>
              <Typography>{props.smallMessage}</Typography>
            </Box>

            <Box my={3}>
              <Divider variant="middle" />
            </Box>
            <TrackingCode response={rcrr} />
            {/* <OneClickRegistration response={rcrr} /> */}
          </Box>
        </Box>
        <Box py={2}>
          <Divider />
        </Box>
        <Grid container spacing={1} direction="row-reverse">
          <Grid item xs={12} md={6}>
            <Box py={2}>
              <Typography variant="h6">Worker Details</Typography>
            </Box>
            <RequestWorkerContainer
              workerRental={applicationForm.workerRental}
              workerTwoYear={applicationForm.workerTwoYear}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <RequestScheduleDisplay applicationForm={applicationForm} />
            <Box py={2}>
              <Typography variant="h6">Application Details</Typography>
            </Box>
            <Box>
              <Grid container>
                <LabelValueLine title="Full Name" oneLine={oneLine}>
                  {applicationForm?.fullName}
                </LabelValueLine>
                <LabelValueLine title="Mobile" oneLine={oneLine}>
                  {applicationForm?.mobileNumber}
                </LabelValueLine>
                <LabelValueLine title="Other Mobile" oneLine={oneLine}>
                  {applicationForm?.otherMobileNumber}
                </LabelValueLine>
                <LabelValueLine title="Special Notes" oneLine={oneLine}>
                  {applicationForm?.specialNotes}
                </LabelValueLine>
                {/* <LabelValueLine title="Address" oneLine={oneLine}>
                  {applicationForm?.address}
                </LabelValueLine> */}
                {/* <LabelValueLine title="Frequency" oneLine={oneLine}>{applicationForm?.frequency}</LabelValueLine> */}
              </Grid>
            </Box>
          </Grid>
        </Grid>
        {nextButtons}
      </NiceBox>
    </>
  );
};

export default SuccessPage;
