import axios from "axios";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { IApiResBundle } from "estreeh-common-library/src/interfaces/api/res/IApiResBundle";

export const apiGetBundle = async () => {
  console.log("Getting bundle");
  const r = await axios.get<IResponse<IApiResBundle>>(
    process.env.REACT_APP_API_ROOT + "/pub/bundle",
    { withCredentials: true }
  );
  return r.data.data;
};
