import { Button, Icon } from "@material-ui/core";
import React from "react";
import { useTranslation } from "react-i18next";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";

interface Props {
  worker?: IWorkerTwoYear;
}

const FavoriteButton = ({ worker }: Props) => {
  const { t } = useTranslation();
  const add = useStoreActions((a) => a.addToFavTwoYear);
  const remove = useStoreActions((a) => a.removeFromFavTwoYear);
  const favorites = useStoreState((s) => s.favTwoYear.items);
  const exists =
    worker !== undefined &&
    favorites.findIndex((x) => x._id === worker.id) !== -1;
  const heartColor = "#CC6174";

  return (
    <>
      {exists ? (
        <Button
          size="small"
          variant="outlined"
          // color="primary"
          style={{ color: heartColor, borderColor: heartColor }}
          startIcon={<Icon>favorite</Icon>}
          onClick={() => {
            if (worker !== undefined) {
              // eslint-disable-next-line no-restricted-globals
              if (confirm(`Remove ${worker.name} from favorites?`)) {
                remove(worker);
              }
            }
          }}
        >
          {t("button.fav")}
        </Button>
      ) : (
        <Button
          size="small"
          variant="outlined"
          color="primary"
          startIcon={<Icon>favorite_outlined</Icon>}
          onClick={() => {
            if (worker !== undefined) {
              add(worker);
            }
          }}
        >
          {t("button.fav")}
        </Button>
      )}
    </>
  );
};

export default FavoriteButton;
