import { Box, Button, Grid, Icon } from "@material-ui/core";
import React from "react";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import SmallText from "../../common/SmallText";

type ButtonVariant = "text" | "outlined" | "contained";

const Worker2ListCardInfo = (props: { worker: IWorkerTwoYear }) => {
  const worker = props.worker;

  const buttonStyle: React.CSSProperties = {
    margin: "0 4px",
    padding: "0px 8px",
  };
  const buttonVariant: ButtonVariant = "contained";
  const iconFontSize = "inherit";
  const infoGridXs = 6;
  const infoGridMd = 4;

  return (
    <React.Fragment>
      <Grid container>
        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>local_airport</Icon>{" "}
            {worker.experienceYears} years experience
          </SmallText>
        </Grid>

        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>history</Icon> {worker.age} years old
          </SmallText>
        </Grid>
        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>attach_money</Icon>{" "}
            {worker.processFeesBhdFormatted}
          </SmallText>
        </Grid>
        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>work</Icon> {worker.jobTitle}
          </SmallText>
        </Grid>
        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>school</Icon>{" "}
            {worker.educationalLevel}
          </SmallText>
        </Grid>
        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>attach_money</Icon>{" "}
            {worker.monthlySalaryBhdFormatted}
          </SmallText>
        </Grid>

        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>brightness_3</Icon> {worker.religion}
          </SmallText>
        </Grid>
        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>sms</Icon>{" "}
            {worker.demoLanguageSkillsText} Spoken
          </SmallText>
        </Grid>
        <Grid item xs={infoGridXs} md={infoGridMd}>
          <SmallText>
            <Icon fontSize={iconFontSize}>phone</Icon> {worker.phone}
          </SmallText>
        </Grid>
      </Grid>
      <Box mt={2} display="none" textAlign="right">
        <Button
          color="secondary"
          variant={buttonVariant}
          size="small"
          style={buttonStyle}
        >
          CV Details
        </Button>
        <Button
          color="secondary"
          variant={buttonVariant}
          size="small"
          style={buttonStyle}
        >
          <Icon fontSize="inherit">favorite</Icon>
        </Button>
        <Button
          color="primary"
          variant={buttonVariant}
          size="small"
          style={buttonStyle}
        >
          Select
        </Button>
      </Box>
    </React.Fragment>
  );
};

export default Worker2ListCardInfo;
