export function DaysArray(numberOfDays?:number, startDate?:Date):Date[]{
  let start:Date = startDate ? startDate : new Date();
  let days:number = numberOfDays ? numberOfDays : 7;
  let dateArray:Date[] = [];

  for (let t = 0; t < days; t++) {
    let i = new Date(start);
    i.setDate(i.getDate() + t);
    dateArray.push(i);
  }

  return dateArray;
}

export function MonthsArray(numberOfMonths?:number, startDate?: Date):Date[]{
  let start:Date = startDate ? startDate : new Date();
  let months:number = numberOfMonths ? numberOfMonths : 12;
  let dateArray:Date[] = [];

  for (let t = 0; t < months; t++) {
    let i = new Date(start);
    i.setMonth(i.getMonth() + t);
    dateArray.push(i);
  }

  return dateArray;
}