import { Avatar, Box, Grid, Typography } from "@material-ui/core";
import React from "react";
import Branding from "../../../config/Branding";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import FavoriteButton from "../../common/FavoriteButton";

import OrangeChip from "../../common/OrangeChip";
import SelectWorkerButton from "../../common/SelectWorkerButton";

const WorkerTwoYearCardPicTitle = (props: {
  worker: IWorkerTwoYear;
  children?: React.ReactNode;
  picHeight?: number;
  showButtons: boolean;
  onPicTitleClick?: any;
  onReserveButton: React.MouseEventHandler<HTMLButtonElement>;
}) => {
  const worker = props.worker;

  // const linkStyles: CSSProperties = {
  //   display: "inline-flex",
  //   textDecoration: "none",
  // };
  const picWidth = "100%";
  const picHeight = props.picHeight ? props.picHeight : 80;
  const localTransfer =
    worker.isLocalTransfer && false ? (
      <OrangeChip label="Local Transfer" />
    ) : null;

  const buttons = props.showButtons ? (
    <Grid item xs={12} md={2}>
      <Box textAlign="right" px={1} color={Branding.text.secondary}>
        <FavoriteButton worker={worker} />
        <SelectWorkerButton onClick={props.onReserveButton} />
      </Box>
    </Grid>
  ) : null;

  const clickableStyle = props.onPicTitleClick ? { cursor: "pointer" } : {};

  return (
    <Grid container spacing={2} direction="row-reverse">
      {buttons}
      <Grid item xs={8} md={8}>
        <Box pb={1} style={clickableStyle} onClick={props.onPicTitleClick}>
          <Typography style={{ fontWeight: "bold" }}>
            {worker.name} {localTransfer}
          </Typography>
        </Box>
        {props.children}
      </Grid>
      <Grid item xs={4} md={2}>
        <Box
          textAlign="center"
          style={clickableStyle}
          onClick={props.onPicTitleClick}
        >
          <Avatar
            src={process.env.REACT_APP_IMAGES_ROOT + "" + worker.smallPicture}
            style={{
              width: picWidth,
              minHeight: picHeight,
              alignSelf: "center",
            }}
            variant="rounded"
          />
        </Box>
      </Grid>
    </Grid>
  );
};

export default WorkerTwoYearCardPicTitle;
