import React, { Component } from "react";
import {
  Box,
  Collapse,
  Divider,
  FormGroup,
  Grid,
  Icon,
  Typography,
} from "@material-ui/core";

interface IFilterGroup {
  children?: React.ReactNode;
  expanded?: boolean;
  hideDivider?: boolean;
  title: string;
}

class FilterGroup extends Component<IFilterGroup, any> {
  constructor(props: IFilterGroup) {
    super(props);
    this.state = {
      expanded: props.expanded === undefined ? true : props.expanded,
    };

    this.handleCollapseButton = this.handleCollapseButton.bind(this);
  }

  componentDidMount() {}

  handleCollapseButton() {
    this.setState({
      expanded: !this.state.expanded,
    });
  }

  render() {
    const iconSize = "inherit";
    const expandLabel = this.state.expanded ? (
      <Icon fontSize={iconSize}>expand_less</Icon>
    ) : (
      <Icon fontSize={iconSize}>expand_more</Icon>
    );

    const dividerM = this.state.expanded ? 2 : 1;

    const divider =
      this.props.hideDivider === true ? null : (
        <Box my={dividerM} className="transitionDefault">
          <Divider variant="fullWidth" />
        </Box>
      );

    return (
      <Box
      // boxShadow={"0 0 12px " + Branding.light + "ff"}
      // py={1}
      // px={2}
      // my={1}
      // bgcolor="white"
      // borderRadius={10}
      >
        <Box pb={1}>
          <Box
            onClick={this.handleCollapseButton}
            style={{ cursor: "pointer", userSelect:"none" }}
          >
            <Grid container>
              <Grid item xs={10}>
                <Box>
                  <Typography
                    color="textPrimary"
                    style={{ fontWeight: "bold" }}
                  >
                    {this.props.title}
                  </Typography>
                </Box>
              </Grid>
              <Grid item xs={2}>
                <Box textAlign="right">{expandLabel}</Box>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Collapse in={this.state.expanded}>
          <Box>
            <FormGroup>{this.props.children}</FormGroup>
          </Box>
        </Collapse>
        {divider}
      </Box>
    );
  }
}

export default FilterGroup;
