import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import IAgencyTab from "estreeh-common-library/src/interfaces/IAgencyTab";


export function MakeTabsRentalWorkers(agency: IAgency, index: number): number {
  agency.tabs = [];
  agency.tabsRental = [];
  agency.servicesSelectableWorkers?.forEach((service) => {
    if (service.isRentalHourly ||
      service.isRentalMonthly ||
      service.isRentalMonthlyStayOver) {
      let feesText = service.feesText ? service.feesText : "";
      let tab: IAgencyTab = {
        index: index,
        title: service.title,
        feesText: feesText,
        titleWithFees: service.title + " " + feesText,
        isRented: true,
        isRentedHourly: service.isRentalHourly,
        isRentedMonthly: service.isRentalMonthly,
        isTwoYearContract: false,
        isServices: false,
        workersRental: service.workersRental,
        rentalService: service,
        remarks: service.remarks,
      };
      index++;
      agency.tabs.push(tab);
      agency.tabsRental.push(tab);
    }
  });

  return index;
}
