import { FormControl, FormControlLabel, FormLabel, Radio, RadioGroup } from '@material-ui/core'
import React, { ReactElement } from 'react'

interface Props {
  setter: React.Dispatch<React.SetStateAction<number>>;
  numberOfMultiDays: number;
}

export default function RequestDaysScheduleDisplayMode(props: Props): ReactElement {
  const demoRadio = <Radio color="primary" />;
  return (
    <FormControl component="fieldset">
      <FormLabel component="legend">
        Schedule display mode:
      </FormLabel>
      <RadioGroup row defaultValue="1" onChange={(event,value) => {
        props.setter(parseInt(value));
      }}>
        <FormControlLabel
          value="1"
          label="Selected Day"
          control={demoRadio}
        />
        <FormControlLabel
          value={props.numberOfMultiDays.toString()}
          label="Multiple Days"
          control={demoRadio}
        />
        {/* <FormControlLabel
          value={props.numberOfMultiDays.toString()}
          label="Every Week"
          control={demoRadio}
        /> */}
      </RadioGroup>
    </FormControl>
  )
}
