import { Button } from "@material-ui/core";
import { Link } from "@reach/router";
import React from "react";
import { useTranslation } from "react-i18next";

const ReserveWorkerButton = (props: {
  link?: string;
  onClick?: React.MouseEventHandler<HTMLButtonElement>;
}) => {
  const {t} = useTranslation();
  const link = props.link ? props.link : "/login-prompt";
  const linkStyles: React.CSSProperties = {
    display: "inline-flex",
    textDecoration: "none",
  };

  return (
    <React.Fragment>
      {props.onClick === undefined && (
        <Link to={link} style={linkStyles}>
          <Button
            style={{ marginLeft: 8 }}
            size="small"
            color="primary"
            variant="outlined"
          >
            Reserve (FIX ME)
          </Button>
        </Link>
      )}
      {props.onClick !== undefined && (
        <Button
          style={{ marginLeft: 8 }}
          size="small"
          color="primary"
          variant="outlined"
          onClick={props.onClick}
        >
          {t("button.reserve")}
        </Button>
      )}
    </React.Fragment>
  );
};

export default ReserveWorkerButton;
