import { Box, Link, Typography } from "@material-ui/core";
import Branding from "../../config/Branding";
import LoginFormFields from "./LoginFormFields";
// import SignInWithApple from "../mock/SignInWithApple";
// import SignInWithGoogle from "../mock/SignInWithGoogle";
interface Props {
  setIntent: React.Dispatch<React.SetStateAction<"login" | "signUp">>;
  demoLink?: string;
  onLogin: Function;
}

//TODO: FUTURE: Rework. create "Login Modal" component that functions fully

export const LoginForm = (props: Props) => {

  //TODO: Login with Google

  return (
    <Box p={1} maxWidth={640} mx="auto">
      <Box
        my={2}
        py={3}
        borderRadius={Branding.borderRadius.niceRadius}
        boxShadow={Branding.shadow.niceShadow}
        bgcolor="white"
      >
        <Box maxWidth={346} mx="auto" px={2}>
          <Box mb={2} textAlign="center">
            <Typography variant="h6">Login</Typography>
          </Box>
          <LoginFormFields demoLink={props.demoLink} onLogin={props.onLogin} />
          <Box textAlign="center">
            <Typography>
              Don't have an account?
              <br />{" "}
              <Link
                style={{ cursor: "pointer" }}
                onClick={() => {
                  props.setIntent("signUp");
                }}
              >
                Click to sign-up
              </Link>
            </Typography>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};
