import { Grid } from '@material-ui/core';
import { Link } from '@reach/router';
import React from 'react';
import IAgency from 'estreeh-common-library/src/interfaces/IAgency';
import SmallInfo from '../common/SmallInfo';

const AgencyContactInfo = (props:{
  agency:IAgency
}) => {
  const agency = props.agency;
  return (

    <Grid container spacing={0}>
    <Grid item md={4} xs={6}>
      <SmallInfo icon="phone">{agency.telephone}</SmallInfo>
    </Grid>
    <Grid item md={6} xs={12}>
      <SmallInfo icon="email">
        <Link to="">{agency.email}</Link>
      </SmallInfo>
    </Grid>

    <Grid item md={2} xs={12}>
      <SmallInfo icon="location_on">
        <Link to="">Map</Link> {agency.location?.address}
      </SmallInfo>
    </Grid>
  </Grid>
  );
};

export default AgencyContactInfo;