import { Box, Typography } from "@material-ui/core";
import React, { ReactElement } from "react";
import Branding from "../../../config/Branding";
import Dot from "../../common/Dot";

interface Props {
  dotColor?: string;
  text: string;
}

export default function SlotMiniDisplay(props: Props): ReactElement {
  const fontSize = Branding.fontSize.small - 4;
  return (
    <Box
      css={{
        padding: "0",
        margin: "0",
      }}
    >
      <Typography
        style={{
          fontSize: fontSize,
          fontWeight: "bold",
          // textAlign: "center",
          display: "block",
          margin: 0,
          padding: 0,
        }}
      >
        <Dot fontSize={10} color={props.dotColor} px={0.5} /> {props.text}
      </Typography>
    </Box>
  );
}
