import { Chip } from '@material-ui/core';
import React from 'react';

const OrangeChip = (props:{
  label:string;
}) => {
  return (
    <Chip
      component="span"
      label={props.label}
      size="small"
      color="secondary"
      style={{
        borderRadius: 5,
        fontSize: 12,
        height: 20,
        fontWeight:"bold"
      }}
    />
  );
};

export default OrangeChip;