import { IFilterParameter } from "estreeh-common-library/src/interfaces/IFilterParameter";

export const removeFilterParameter = (
  arr: IFilterParameter[],
  key: string
): IFilterParameter[] => {
  const index = arr.findIndex((filter) => {
    if (filter.key === key) {
      return true;
    }
    return false;
  });
  if (index > -1) {
    arr.splice(index, 1);
  }
  return arr;
};
