import * as Factory from "factory.ts";
import Faker from "faker";
import IMonthlySlot from "estreeh-common-library/src/interfaces/IMonthlySlot";

const MonthlySlotFactory = Factory.Sync.makeFactory<IMonthlySlot>({
  id: Factory.each((i) => i + 1),
  availabilityStatus: Factory.each(() =>
    Faker.random.arrayElement([
      "Free",
      "Free",
      "Free",
      "Free",
      "Booked",
      "Booked",
      // "Off",
    ])
  ),
  availabilityDisplayText: "",
  color: "",
  date: new Date(),
  clickable: false,
  selected: false,
});

export default MonthlySlotFactory;
