import { Box, Button } from "@material-ui/core";
import { FilterList, ImportExport } from "@material-ui/icons";
import { Link, RouteComponentProps } from "@reach/router";
import React from "react";
import SideFilter from "./SideFilter";

interface ISideFilterContainerParams {
  is2Year: boolean;
  isLeased: boolean;
  isMd: boolean;
  showSortButton: boolean;
}

interface ISideFilterContainer
  extends RouteComponentProps<ISideFilterContainerParams> {
  }

const SideFilterContainer = (props: ISideFilterContainer) => {
  // const isMd = useMediaQuery(MyTheme.breakpoints.up("md"));
  const isMd = props.isMd !== undefined ? props.isMd : true;
  const is2Year = props.is2Year ? props.is2Year : false;
  const isLeased = props.isLeased ? props.isLeased : false;
  const linkStyles: React.CSSProperties = {
    // display: "block",
    textDecoration: "none",
  };
  const buttonStyles: React.CSSProperties = {
    width: props.showSortButton === true ? "50%" : "100%",
    borderRadius: 0,
    fontWeight: "bold",
    // border:0,
    // borderBottom:"1px solid #000",
    backgroundColor: "white",
  };
  return (
    <React.Fragment key="sideFilterHooray">
      {isMd && <SideFilter is2Year={is2Year} isLeased={isLeased} isMd={isMd} />}
      {!isMd && (
        <Box pt={1}>
 {props.showSortButton === true && (          <Link to="." style={linkStyles}>
            <Button style={{ ...buttonStyles, borderRightColor: "#FFF" }} variant="outlined">
              <ImportExport />
              &nbsp;Sort
            </Button>
          </Link>)}
          <Link to={props.is2Year ? "/agencies/contract-filters" : "/agencies/leased-filters"} style={linkStyles}>
            <Button
              style={{ ...buttonStyles }}
              variant="outlined"
            >
              <FilterList />
              &nbsp;Filter
            </Button>
          </Link>
        </Box>
      )}
    </React.Fragment>
  );
};

export default SideFilterContainer;
