import { Box, Container, Grid, Link, Typography } from "@material-ui/core";
import { Clear } from "@material-ui/icons";
import { navigate, RouteComponentProps } from "@reach/router";
import React from "react";
import Branding from "../../config/Branding";

interface Params {
  title: string;
  showCloseButton: boolean;
  showCancelText: boolean;
  showBackButton: boolean;
  children: React.ReactNode;
  rightText: string;
  rightOnClick: React.MouseEventHandler<any>;
}

interface Props extends RouteComponentProps<Params> {}

const SiteHeadless = (props: Props) => {
  const linkStyles: React.CSSProperties = {
    color: Branding.text.primary,
    cursor: "pointer",
    userSelect:"none",
  };
  return (
    <Box bgcolor="#ddd" boxShadow={Branding.shadow.niceShadow}>
      <Container>
        <Box p={1}>
          <Grid container spacing={0}>
            {props.showCloseButton && (
              <Grid item xs={1}>
                <Clear fontSize="small" />
              </Grid>
            )}
            {props.showCancelText && (
              <Grid item xs={4}>
                <Typography variant="body2">
                  <Link style={linkStyles} onClick={() => navigate(-1)}>
                    Cancel
                  </Link>
                </Typography>
              </Grid>
            )}
            <Grid item xs={4}>
              <Typography
                variant="body2"
                style={{
                  fontWeight: "bold",
                  textAlign: "center",
                  textTransform:"uppercase",
                }}
              >
                {props.title}
              </Typography>
            </Grid>
            <Grid item xs={4}>
              <Box textAlign="right">
                <Typography variant="body2">
                  <Link style={linkStyles} onClick={props.rightOnClick}>
                    {props.rightText}
                  </Link>
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </Container>
    </Box>
  );
};

export default SiteHeadless;
