import React, { ReactElement, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { TranslationPack } from "estreeh-common-library/src/types/TranslationPack";

import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";
import AppDataProvider from "./AppDataProvider";

interface Props {
  classes: any;
}

export default function ToolboxState(props: Props): ReactElement {
  const loadFromApi = useStoreActions((action) => action.loadFromApi);
  const setDataLoading = useStoreActions((a) => a.dataLoading.setItem);
  const dataLoading = useStoreState((state) => state.dataLoading.item);
  const setTranslationPack = useStoreActions(
    (action) => action.translationPack.setItem
  );
  const pack:TranslationPack | undefined = useTranslation();
  const [ll, setLl] = useState(false);

  useEffect(() => {
    if (dataLoading === false && ll === false && pack !== undefined) {
      setLl(true);
      setDataLoading(true);
      loadFromApi(pack);
    }
  }, [loadFromApi, dataLoading, pack, setTranslationPack, setDataLoading, ll]);

  return <AppDataProvider classes={props.classes} />;
}
