import { Box, Button, TextField } from "@material-ui/core";
import { FormikProps } from "formik";
import React, { ReactElement } from "react";
import { IApiReqRegisterUser } from "estreeh-common-library/src/interfaces/api/req/IApiReqRegisterUser";

interface Props {
  formik: FormikProps<IApiReqRegisterUser>;
}

export default function RegisterCustomerForm({ formik }: Props): ReactElement {
  
  return (
    <form onSubmit={formik.handleSubmit} autoComplete="off">
      <Box my={2}>
      Your mobile: {formik.values.mobile}
      </Box>
      <Box my={2} maxWidth={420} mx="auto">
        <TextField
          variant="outlined"
          label="Your Name"
          error={formik.touched.fullName && formik.errors.fullName !== undefined}
          helperText={formik.touched.fullName && formik.errors.fullName}
          autoComplete="new-password" 
          {...formik.getFieldProps("fullName")}
        />
        <TextField
          variant="outlined"
          label="Password"
          type="password"
          autoComplete="new-password" 
          error={formik.touched.password && formik.errors.password !== undefined}
          helperText={formik.touched.password && formik.errors.password}
          {...formik.getFieldProps("password")}
        />
        <TextField
          variant="outlined"
          label="Confirm Password"
          type="password"
          autoComplete="new-password"
          error={formik.touched.confirmPassword && formik.errors.confirmPassword !== undefined}
          helperText={formik.touched.confirmPassword && formik.errors.confirmPassword}
          {...formik.getFieldProps("confirmPassword")}
        />
      </Box>
      <Button type="submit" color="primary" variant="contained" disabled={formik.isSubmitting}>
        Sign-Up
      </Button>
    </form>
  );
}
