import { Box, Grid } from "@material-ui/core";
import React, { ReactElement } from "react";
import Branding from "../../config/Branding";
import { useStoreState } from "../../helpers/StoreTypedHooks";
import FooterMenuItem from "./FooterMenuItem";
import FpSection from "./frontpage/FpSection";

interface Props {}

export default function Footer1(props: Props): ReactElement {
  const menu = useStoreState((s) => s.topNavLinks.items);

  //TODO: Implement footer1
  //TODO: Safety Menu URL
  //TODO: FAQ Menu URL
  /*TODO:
- Page footer 1
--- T&C
--- Our Policy
--- Contact Us
*/

  return (
    <FpSection bgColor={Branding.dark} py={5}>
      <Box fontSize="80%" textAlign="center">
        <Grid container justify="center" direction="row-reverse">
        {menu.map((m, index) => (
          <FooterMenuItem
            key={m.title + "_" + m.to}
            menu={m}
            last={(index+1) >= menu.length}
          />
        ))}
        </Grid>
      </Box>
    </FpSection>
  );
}
