import { Box, Icon } from "@material-ui/core";
import React from "react";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import WorkerTwoYearCardPicTitle from "./Worker2ListCardPicTitle";
import LabelValueLine from "../../common/LabelValueLine";
import LabelValuesContainer from "../../common/LabelValuesContainer";
import Branding from "../../../config/Branding";
import { localTransferExplanation } from "../../../config/FilterOptions";
import SmallText from "../../common/SmallText";

const WorkerTwoYearDetailsTopSection = (props: {
  worker: IWorkerTwoYear;
  showButtons?: boolean;
  onReserveButton: React.MouseEventHandler<HTMLButtonElement>;
}) => {
  const worker = props.worker;

  const oneLine = true;
  const lt = 2;
  const lv = 10;
  const showButtons =
    props.showButtons !== undefined ? props.showButtons : true;

  return (
    <Box pb={2}>
      <WorkerTwoYearCardPicTitle
        worker={worker}
        picHeight={140}
        showButtons={showButtons}
        onReserveButton={props.onReserveButton}
      >
        <LabelValuesContainer>
          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Reference #"
          >
            {worker.referenceId}
          </LabelValueLine>
          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Job Title"
          >
            {worker.jobTitle}
          </LabelValueLine>
          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Salary"
          >
            {worker.monthlySalaryBhdFormatted}
          </LabelValueLine>
          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Processing Fees"
          >
            {worker.processFeesBhdFormatted}
          </LabelValueLine>

          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Contract Period"
          >
            2 Years
          </LabelValueLine>
          {/* {worker.isLocalTransfer && (
            <LabelValueLine
              oneLineTitleSizeMd={lt}
              oneLineValueSizeMd={lv}
              oneLine={oneLine}
              title={
                <React.Fragment>
                  <Icon fontSize="inherit">warning</Icon> Available in Bahrain
                </React.Fragment>
              }
              style={{ color: Branding.text.warning }}
            >
              {localTransferExplanation}
            </LabelValueLine>
          )} */}
          {worker.isLocalTransfer && (
            <Box>
              <SmallText style={{ color: Branding.text.warning }}>
                <Icon fontSize="inherit">warning</Icon>{" "}
                {localTransferExplanation}
              </SmallText>
            </Box>
          )}
        </LabelValuesContainer>
      </WorkerTwoYearCardPicTitle>
    </Box>
  );
};

export default WorkerTwoYearDetailsTopSection;
