import { IFilterParameter } from "estreeh-common-library/src/interfaces/IFilterParameter";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import _ from "lodash";

export const FilterTwoYearWorkers = (
  workers: IWorkerTwoYear[],
  filterParameters: IFilterParameter[]
) => {
  if (filterParameters.length < 1) {
    return workers;
  }

  let resultWorkers: IWorkerTwoYear[] = workers.map((w) => w);
  filterParameters.forEach((filter) => {
    resultWorkers = _.filter(resultWorkers, (worker) => {
      const key = filter.key as keyof IWorkerTwoYear;
      const value = worker[key];
      if (
        filter.array !== undefined &&
        filter.lt === undefined &&
        filter.gt === undefined
      ) {
        if (typeof value === "string") return filter.array.includes(value);
      }

      if (
        filter.array === undefined &&
        filter.gt !== undefined &&
        filter.lt !== undefined
      ) {
        // const key = filter.key as keyof IWorkerTwoYear;
        if (typeof value === "number")
          return value >= filter.gt && value <= filter.lt;
      }
      return true;
    });
  });

  return resultWorkers;
};
