import { Box, Grid, Typography } from "@material-ui/core";
import { Variant } from "@material-ui/core/styles/createTypography";
import { CSSProperties } from "@material-ui/core/styles/withStyles";
import { Router } from "@reach/router";
import { RouteComponentProps } from "@reach/router";
import React from "react";
import Branding from "../../config/Branding";
import FpSection from "../common/frontpage/FpSection";
import FpStep from "../common/frontpage/FpStep";
import { WizardLand } from "../wizard/WizardLand";
import WizardStartRental from "../wizard/WizardStartRental";
import click from "../../images/click.svg";
import check from "../../images/check.svg";
import fill from "../../images/fill.svg";

interface Params {
  isMd: boolean;
}

interface Props extends RouteComponentProps<Params> {}

/*TODO:

- Page middle section:
--- LMRA Badge
--- Safety Info

*/

const LandingPage = (props: Props) => {
  const hVariant: Variant | "inherit" = "h4";
  const hStyle: CSSProperties = {
    marginBottom: 32,
    textAlign:"center"
  };

  return (
    <Box>
      <Box mx="auto" maxWidth={700} p={2} pb={7}>
        <Box marginTop={-11}>
          <Router primary={false}>
            <WizardLand path="/" isMd={props.isMd} />
            <WizardStartRental path="lease-hourly" period="hour" />
            <WizardStartRental path="lease-monthly" period="month" />
          </Router>
        </Box>
      </Box>
      <FpSection>
        <Typography variant={hVariant} style={hStyle}>
          Who we are
        </Typography>
        <Box textAlign="justify" maxWidth={660} mx="auto" style={{ 
          textAlignLast:"center"
         }}>
          <Typography>
          We are a passionate team driven by the desire to improve the quality of life of Bahraini Citizens and to ease the burdens of our loved ones who are searching and applying for a Domestic Worker. 
         <br/><br/>
         Our mission is to provide a high-quality web-based and mobile software product that connects Bahraini Citizens to Domestic Manpower Agencies. 
         <br/><br/>
         Our vision is to help Bahrain to be the first country in GCC with an Infrastructure of Care, by providing systems and services that help families meet their caregiving needs.
          </Typography>
        </Box>
      </FpSection>
      <FpSection bgColor={Branding.lighter}>
        <Typography variant={hVariant} style={hStyle}>
          How to start
        </Typography>
        <Box maxWidth={780} mx="auto">
          <Grid container spacing={3} alignContent="center" alignItems="center">
            <FpStep title="Step 1:" image={click}>
              Choose a worker
            </FpStep>
            <FpStep title="Step 2:" image={fill}>Fill your details</FpStep>
            <FpStep title="Step 3:" image={check}>Track status!</FpStep>
          </Grid>
        </Box>
      </FpSection>
    </Box>
  );
};

export default LandingPage;
