import IHourlySlot from "estreeh-common-library/src/interfaces/IHourlySlot";
import dateFormat from "dateformat";
import SlotMiniDisplay from "./SlotMiniDisplay";

const HourlySlotMiniDisplay = (props: { slot: IHourlySlot }) => {
  const slot = props.slot;
  // const militaryFormat = "HH:MM";
  const simpleFormat = "h tt";
  const text = dateFormat(slot.hourlySlotDefinition?.from, simpleFormat);
  // const to = dateFormat(slot.hourlySlotDefinition?.to, "h:MM tt");

  return <SlotMiniDisplay dotColor={slot.colorDot} text={text} />;
};

export default HourlySlotMiniDisplay;
