import { Chip } from "@material-ui/core";
import React from "react";
import Branding from "../../config/Branding";
import CountryFlag from "./CountryFlag";
import { ICountryChip } from "estreeh-common-library/src/interfaces/ICountryChip";

const CardChips = (props: {
  variant?: "chips" | "text";
  titles: string[];
  countryChips?: ICountryChip[];
  showFees: boolean;
}) => {
  const variant = props.variant ? props.variant : "chips";
  const showCountryFlag = props.countryChips !== undefined;
  const chipStyle: React.CSSProperties = {
    background: Branding.lightest,
    margin: "4px 2px 4px 0",
    height: 22,
    fontSize: 11,
    borderRadius: Branding.borderRadius.niceRadius,
  };

  let chips: React.ReactNode;
  if (variant === "chips" && !showCountryFlag) {
    chips = props.titles.map((title) => (
      <Chip key={title} label={title} size="small" style={chipStyle} />
    ));
  }
  if (variant === "chips" && showCountryFlag && props.countryChips) {
    chips = props.countryChips.map((country) => (
      <Chip
        key={country.country}
        label={<CountryFlag country={country} showFees={props.showFees} />}
        size="small"
        style={chipStyle}
      />
    ));
  }
  if (variant === "text") {
    chips = props.titles.join(", ");
  }
  return <React.Fragment>{chips}</React.Fragment>;
};

export default CardChips;
