import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  Typography,
} from "@material-ui/core";
import { RouteComponentProps, navigate } from "@reach/router";
import { ReactElement, useEffect, useState } from "react";
import { IApplicationForm } from "estreeh-common-library/src/interfaces/IApplicationForm";
import IPaymentSummaryItem from "estreeh-common-library/src/interfaces/IPaymentSummaryItem";
import NiceBox from "../common/NiceBox";
import ScrollToTop from "../common/ScrollToTop";
import RequestFormGeneral from "./RequestFormGeneral";
import RequestPaymentSummary from "./RequestPaymentSummary";
import RequestWorkerContainer from "./RequestWorkerContainer";
// import { ApplicationFormFactory } from "../../factories/ApplicationFormFactory";
import RequestScheduleDisplay from "./RequestScheduleDisplay";
// import TermsAndConditionsItem from "../common/TermsAndConditionsItem";
import isEmail from "validator/es/lib/isEmail";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";
import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";

interface IRequestWorkerPageParams {
  isTwoYear: boolean;
  isRentalHourly: boolean;
  isRentalMonthly: boolean;
  link: string;
  isMd: boolean;
  guid: string;
}

//TODO: Provide worker ID in the page param so user can resume / refresh directly
interface IRequestWorkerPage
  extends RouteComponentProps<IRequestWorkerPageParams> {}

export default function RequestWorkerPage(
  props: IRequestWorkerPage
): ReactElement {
  // const selectedWorkerTwoYear = useStoreState(
  //   (s) => s.selectedWorkerTwoYear.item
  // );
  const selectedWorkerRental = useStoreState(
    (s) => s.selectedWorkerRental.item
  );
  const applicationForm = useStoreState((s) => s.applicationForm.item);
  const user = useStoreState((s) => s.user.item);
  const workersTwoYear = useStoreState((s) => s.workerTwoYear.items);
  const agencies = useStoreState((s) => s.agency.items);

  const setAppForm = useStoreActions((a) => a.applicationForm.setItem);

  //TODO: Better messages
  //TODO: Message Indication of the non-refundable amount

  const isRentalHourly =
    props.isRentalHourly !== undefined ? props.isRentalHourly : false;
  const isRentalMonthly =
    props.isRentalMonthly !== undefined ? props.isRentalMonthly : false;
  const link = props.link !== undefined ? props.link : "#";
  const isRental = isRentalHourly || isRentalMonthly;
  let workerTwoYear: IWorkerTwoYear | undefined = undefined;

  if (props.isTwoYear === true) {
    workerTwoYear = workersTwoYear.find((x) => x.guid === props.guid);
    if (workerTwoYear) {
      workerTwoYear.agency = agencies.find(
        (x) => x._id === workerTwoYear?.agencyId
      );
    }
  }
  let workerRental = isRental ? selectedWorkerRental : undefined;

  const [paymentItems, setPaymentItems] = useState<IPaymentSummaryItem[]>([]);

  const borderColor = "#0000001f";
  const border = "1px solid " + borderColor;
  const headerVariant = "h6";
  const showRentalFormFields = props.isTwoYear ? false : true;

  if (isRentalHourly) {
    setPaymentItems([
      {
        title: "Service Charges (Incl. VAT)",
        amount: 0.4,
      },
      {
        title: "Worker Charges",
        amount: 8,
      },
    ]);
  }
  if (isRentalMonthly) {
    setPaymentItems([
      {
        title: "Service Charges (Incl. VAT)",
        amount: 3.1,
      },

      {
        title: "Worker Charges",
        amount: 200,
      },
      {
        title: "Delivery Charges",
        amount: 25,
      },
    ]);
  }

  // let applicationForm = ApplicationFormFactory.build();

  //TODO: Application Form to bring options from previous pages (Frequency, schedule, etc)

  const initialAppForm: IApplicationForm =
    applicationForm !== undefined
      ? applicationForm
      : {
          fullName: user ? user.fullName : "",
          mobileNumber: user ? user.mobile : "",
          otherMobileNumber: user ? user.mobile2 : "",
          email: user ? user.email : "",
          paymentRemarks: "",
          paymentSummaryItems: [],
          selectedHoursFrequency: "One Time",
          created: new Date(),
          modified: new Date(),
          termsAccepted: false,
        };

  initialAppForm.workerTwoYear = workerTwoYear;

  // const [localApplicationForm, setLocalApplicationForm] =
  //   useState<IApplicationForm>(initialAppForm);

  const selectedHours = isRentalHourly
    ? initialAppForm.selectedHours
    : undefined;
  const selectedMonths = isRentalMonthly
    ? initialAppForm.selectedMonths
    : undefined;

  const paymentRemarks =
    props.isTwoYear === true
      ? "Once the application has been sent, the agency will contact you within 1 business day."
      : "Currently we only accept payment as cash on delivery.";

  const onFormSubmit = (values: IApplicationForm) => {
    values.paymentSummaryItems = paymentItems;
    values.paymentRemarks = paymentRemarks;

    transformAppForm(values);
    // setLocalApplicationForm(values);
    setAppForm(values);
    navigate(link);
  };

  const mobRegex = /^(((\+)|(00))973)?\d{8}$/;

  useEffect(() => {
    axios
      .get<IResponse<IPaymentSummaryItem[]>>(
        process.env.REACT_APP_API_ROOT +
          "/pub/workertwoyear/cost/" +
          workerTwoYear?._id
      )
      .then((response) => {
        console.log("Loaded cost breakdown");
        setPaymentItems(response.data.data);
      })
      .catch((reason) => {
        console.log(reason);
      });
  }, [setPaymentItems, workerTwoYear]);

  return (
    <>
      <ScrollToTop />
      <NiceBox p={3}>
        <Grid container spacing={2} direction="row-reverse">
          <Grid item xs={12} md={7}>
            <Box>
              <Box pb={1}>
                <Typography variant={headerVariant}>
                  Selected Worker Details
                </Typography>
              </Box>
              <RequestWorkerContainer
                workerTwoYear={workerTwoYear}
                workerRental={workerRental}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={5}>
            {user !== undefined && (
              <Formik
                validationSchema={Yup.object({
                  fullName: Yup.string().required("Required"),
                  mobileNumber: Yup.string()
                    .matches(mobRegex, "Invalid mobile number")
                    .required("Required"),
                  otherMobileNumber: Yup.string().matches(
                    mobRegex,
                    "Invalid mobile number"
                  ),
                  // email: Yup.string().email("Invalid email address"),
                  termsAccepted: Yup.boolean().required("Required").isTrue(),
                })}
                initialValues={initialAppForm}
                onSubmit={(values, { setSubmitting }) => {
                  setSubmitting(false);
                  onFormSubmit(values);
                }}
              >
                {(formik) => (
                  <form onSubmit={formik.handleSubmit}>
                    <Box
                      pr={props.isMd === true ? 2 : 0}
                      borderRight={props.isMd === true ? border : "none"}
                    >
                      <RequestScheduleDisplay
                        selectedHours={selectedHours}
                        selectedMonths={selectedMonths}
                        selectedHoursFrequency={
                          initialAppForm.selectedHoursFrequency
                        }
                      />
                      <RequestFormGeneral
                        showFrequency={showRentalFormFields}
                        showAddress={showRentalFormFields}
                        showEquipmentOption={false}
                        formik={formik}
                      />
                      {isRentalHourly && (
                        <Box py={1}>
                          <FormControlLabel
                            label="Bring Cleaning Tools (BHD 2 / Day)"
                            control={<Checkbox color="primary" />}
                          />
                        </Box>
                      )}
                      {isRentalMonthly && (
                        <Box py={1}>
                          <FormControlLabel
                            label="Bring Cleaning Tools (BHD 30 / Month)"
                            control={<Checkbox color="primary" />}
                          />
                          <FormControlLabel
                            label="Extra 2 hours per day (BHD 30 / Month)"
                            control={<Checkbox color="primary" />}
                          />
                          <FormControlLabel
                            label="Bring own Food (BHD 60 / Month)"
                            control={<Checkbox color="primary" />}
                          />
                        </Box>
                      )}
                      <RequestPaymentSummary
                        paymentItems={paymentItems}
                        workerRental={workerRental}
                        remarks={paymentRemarks}
                      />
                      <Box py={1}>
                        {/* <TermsAndConditionsItem
                          title="Terms & Conditions"
                          name="termsAccepted"
                          formik={formik}
                        /> */}
                        {/* <TermsAndConditionsItem title="Local Transfer Terms & Conditions" />
                      <TermsAndConditionsItem title="LMRA Terms & Conditions" /> */}
                        {/* <TermsAndConditionsItem
                          verb="I have read"
                          title="Personal Safety Guidelines"
                        /> */}
                      </Box>
                      <Box textAlign="center" pb={6} pt={2}>
                        <Button
                          size="large"
                          variant="contained"
                          color="primary"
                          type="submit"
                        >
                          Continue
                        </Button>
                      </Box>
                    </Box>
                  </form>
                )}
              </Formik>
            )}
          </Grid>
        </Grid>
      </NiceBox>
    </>
  );
}

const transformAppForm = (a: IApplicationForm) => {
  if (a.email && !isEmail(a.email)) {
    a.email = undefined;
  }

  return a;
};
