import axios from "axios";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { IUser } from "estreeh-common-library/src/interfaces/IUser";

export const apiGetUser = async (): Promise<IUser | undefined> => {
  console.log("Getting user");
  const r = await axios.get<IResponse<IUser | undefined>>(
    process.env.REACT_APP_API_ROOT + "/auth/me/customer-fe",
    { withCredentials: true }
  );
  const user = r.data.data;
  return user;
  // if (user !== undefined)
  //   if (user.role === "Customer") {
  //     return user;
  //   } else {
  //     await apiGetLogout();
  //   }
  // return undefined;
};
