import { Box, Grid } from "@material-ui/core";
import React from "react";
import SmallText from "./SmallText";

const LabelValueLine = (props: {
  title: string | React.ReactNode;
  oneLine?: boolean;
  children?: React.ReactNode;
  style?: React.CSSProperties;
  oneLineTitleSizeMd?: 1 | 2 | 3;
  oneLineValueSizeMd?: 9 | 10 | 11;
}) => {
  const oneLineTitleSizeMd = props.oneLineTitleSizeMd
    ? props.oneLineTitleSizeMd
    : 3;
  const oneLineValueSizeMd = props.oneLineValueSizeMd
    ? props.oneLineValueSizeMd
    : 9;
  const titleSizeMd = props.oneLine ? oneLineTitleSizeMd : 3;
  const valueSizeMd = props.oneLine ? oneLineValueSizeMd : 3;
  const valueSizeXs = 6;
  const titleSizeXs = 6;
  const paddingBottom = 0.5;

  return (
    <React.Fragment>
      <Grid item xs={titleSizeXs} md={titleSizeMd}>
        <Box pb={paddingBottom}>
          <SmallText style={{ fontWeight: "bold", ...props.style }}>
            {props.title}
          </SmallText>
        </Box>
      </Grid>
      <Grid item xs={valueSizeXs} md={valueSizeMd}>
        <Box pb={paddingBottom}>
          <SmallText style={props.style}>
            {props.children !== undefined && <>{props.children}</>}
            {props.children === undefined && <>&#8212;</>}
          </SmallText>
        </Box>
      </Grid>
    </React.Fragment>
  );
};

export default LabelValueLine;
