import { Avatar, Box, Grid, Typography } from "@material-ui/core";
import React from "react";
import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import SmallText from "../common/SmallText";
import { Link } from "@reach/router";
import NiceBox from "../common/NiceBox";
import AgencyRatingInfo from "./AgencyRatingInfo";
import OrangeChip from "../common/OrangeChip";
import AgencyTabRentalChips from "./AgencyTabRentalChips";
import CardChips from "../common/CardChips";
import { useTranslation } from "react-i18next";
import { ICountryChip } from "estreeh-common-library/src/interfaces/ICountryChip";
import IAgencyTab from "estreeh-common-library/src/interfaces/IAgencyTab";
import { CountryCodes } from "../../config/CountryCodes";

const AgencyCard = (props: {
  agency: IAgency;
  showContractInfo?: boolean;
  showTabChips?: boolean;
  showRating: boolean;
}) => {
  const { t, i18n } = useTranslation();
  const isArabic = i18n.language === "ar";
  const agency = props.agency;
  // const smallInfoGrid = 3;

  const newChip = agency.new ? <OrangeChip label="New" /> : null;

  let nationalitiesTitles = agency.nationalities?.map((nationality) =>
    t("nationality." + nationality.toLowerCase())
  );
  let nationalitiesText = nationalitiesTitles?.join(", ");

  const twoYearsCountryChips: ICountryChip[] = makeCountryChipsFromAgencyTabs(
    agency.tabsTwoYearWorkers,
    t
  );

  // const twoYearTabsTitles = agency.tabsTwoYearWorkers.map(
  //   (tab) =>
  //     t("nationality." + tab.title.toLowerCase()) + " (" + tab.feesText + ")"
  // );

  // let servicesText = serviceTitles.join(", ");

  // const id = agency._id? agency._id : "";

  return (
    <NiceBox>
      <Grid container spacing={2}>
        <Grid item xs={3} md={2}>
          <Box>
            <Link to={agency.slug}>
              <Avatar
                variant="rounded"
                src={process.env.REACT_APP_IMAGES_ROOT + "" + agency.logo}
                style={{
                  flexShrink: 0,
                  width: "100%",
                  minHeight: 109,
                }}
              />
            </Link>
          </Box>
        </Grid>
        <Grid item xs={9} md={8}>
          <Box pb={0}>
            <Typography style={{ fontSize: "110%", fontWeight: "bold" }}>
              <Link
                to={agency.slug}
                style={{ textDecoration: "none" }}
              >
                {isArabic ? agency.titleAr : agency.title}
                {/* ({agency.workersTwoYear?.length} Workers) */}
              </Link>{" "}
              {newChip}
            </Typography>
          </Box>
          {props.showContractInfo && (
            <Box>
              <SmallText>
                <CardChips
                  titles={[]}
                  countryChips={twoYearsCountryChips}
                  variant="chips"
                  showFees={false}
                />
              </SmallText>
            </Box>
          )}

          {props.showTabChips && props.agency.servicesSelectableWorkers && (
            <Box>
              <SmallText>
                <AgencyTabRentalChips
                  services={props.agency.servicesSelectableWorkers}
                  showContract={false}
                  showRental={true}
                  renderAsText={true}
                  showPriceOnly={true}
                />
              </SmallText>
            </Box>
          )}
          {!props.showContractInfo && false && (
            <SmallText>{nationalitiesText}</SmallText>
          )}
          {!props.showContractInfo && (
            <SmallText>{props.agency.tabsRental[0].remarks}</SmallText>
          )}
          {props.showRating && (<AgencyRatingInfo agency={agency} />)}
          {/* <SmallText>&nbsp;</SmallText> */}
          {/* <AgencyContactInfo agency={agency} /> */}
        </Grid>
        {/* <Grid item xs={12} md={2}>
          <Box textAlign="right" px={1} color={Branding.text.secondary}>
            <FavoriteButton />
          </Box>
        </Grid> */}
      </Grid>
    </NiceBox>
  );
};

export default AgencyCard;

export const makeCountryChipsFromAgencyTabs = (
  agencyTabs: IAgencyTab[],
  t: Function
): ICountryChip[] => {
  return agencyTabs.map((tab) => {
    const code = CountryCodes.find(
      (x) => x.country_name === tab.title
    )?.country_code;
    return {
      country: tab.title,
      countryCode: code !== undefined ? code : "XX",
      titleTranslated: t("nationality." + tab.title.toLowerCase()),
      titleTranslatedWithFees:
        t("nationality." + tab.title.toLowerCase()) + " (" + tab.feesText + ")",
    };
  });
};
