import { Box, Button, TextField } from "@material-ui/core";
import { FormikProps } from "formik";
import React, { ReactElement } from "react";
import { IApiReqUpdateMyProfile } from "estreeh-common-library/src/interfaces/api/req/IApiReqUpdateMyProfile";

interface Props {
  formik: FormikProps<IApiReqUpdateMyProfile>;
  mobile?: string;
}

export default function MyProfileForm({ formik, mobile }: Props): ReactElement {
  return (
    <form onSubmit={formik.handleSubmit} autoComplete="off">
      <Box my={3} fontSize="80%">Your registered mobile is <strong>{mobile}</strong></Box>
      <Box my={2}>
        <TextField
          variant="outlined"
          label="Full Name"
          error={
            formik.touched.fullName && formik.errors.fullName !== undefined
          }
          helperText={formik.touched.fullName && formik.errors.fullName}
          autoComplete="new-password"
          {...formik.getFieldProps("fullName")}
        />
        {/* <TextField
          variant="outlined"
          label="Email"
          error={formik.touched.email && formik.errors.email !== undefined}
          helperText={formik.touched.email && formik.errors.email}
          autoComplete="new-password"
          {...formik.getFieldProps("email")}
        /> */}
      </Box>
      <Button
        type="submit"
        color="primary"
        variant="contained"
        disabled={formik.isSubmitting}
      >
        Save
      </Button>
    </form>
  );
}
