import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import { MakeTabsTwoYearWorkers } from "./MakeTabsTwoYearWorkers";

export const AssignTwoYearWorkersToAgency = (
  agency: IAgency,
  workers?: IWorkerTwoYear[],
  _tabsIndex?: number
): number => {
  let tabsIndex = _tabsIndex !== undefined ? _tabsIndex : 0;
  agency.workersTwoYear = [];
  if (agency.hasTwoYearWorkerServices) {
    const workersTwoYear = workers
      ?.slice(0)
      .filter((x) => x.agencyId === agency._id);
    agency.workersTwoYear = workersTwoYear;
    if (agency.workersTwoYear !== undefined) {
      tabsIndex = MakeTabsTwoYearWorkers(agency, tabsIndex);
    }
  }

  return tabsIndex;
};
