import {
  Checkbox,
  createStyles,
  FormControl,
  Input,
  InputLabel,
  ListItemText,
  makeStyles,
  MenuItem,
  Select,
  Theme,
} from "@material-ui/core";
import { monthPickerMonths } from "../../../config/DemoItems";
import React, { ReactElement, useState } from "react";
import { useTranslation } from "react-i18next";

interface Props {}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    // formControl: {
    //   marginTop: theme.spacing(0.5),
    //   minWidth: 220,
    // },
    formControl: {
      margin: theme.spacing(1),
      minWidth: 220,
      // maxWidth: 320,
      maxWidth:"95%",
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
    chips: {
      display: "flex",
      flexWrap: "wrap",
    },
    chip: {
      margin: 2,
    },
    noLabel: {
      marginTop: theme.spacing(3),
    },
  })
);

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      // maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      maxHeight: ITEM_HEIGHT * 6.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

export default function WizardMonthMultiplePicker(props: Props): ReactElement {
  const {t} = useTranslation();
  const classes = useStyles();
  const [selectedMonths, setSelectedMonths] = useState<string[]>([]);

  const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setSelectedMonths(event.target.value as string[]);
  };

  // const handleChangeMultiple = (
  //   event: React.ChangeEvent<{ value: unknown }>
  // ) => {
  //   const { options } = event.target as HTMLSelectElement;
  //   const value: string[] = [];
  //   for (let i = 0, l = options.length; i < l; i += 1) {
  //     if (options[i].selected) {
  //       value.push(options[i].value);
  //     }
  //   }
  //   setSelectedMonths(value);
  // };

  const label = t("select-month");

  return (
    <FormControl className={classes.formControl} variant="outlined">
      <InputLabel id="month-label">{label}</InputLabel>
      <Select
        labelId="month-label"
        label={label}
        multiple
        value={selectedMonths}
        onChange={handleChange}
        input={<Input />}
        MenuProps={MenuProps}
        renderValue={(selected) => (selected as string[]).join(', ')}
      >
        {monthPickerMonths.map((month) => (
          <MenuItem key={month} value={month}>
            <Checkbox color="primary" checked={selectedMonths.indexOf(month) > -1} />
            <ListItemText primary={month} />
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}
