import { Typography } from '@material-ui/core'
import { IStatus } from 'estreeh-common-library/src/interfaces/IStatus'
import React, { ReactElement } from 'react'
import Branding from '../../config/Branding'

interface Props {
  status:IStatus;
}

export default function RequestStatusPill({status}: Props): ReactElement {
  return (
    <Typography
    style={{
      textAlign: "center",
      padding: 4,
      marginBottom: 8,
      background: status.bgColor,
      borderRadius: Branding.borderRadius.niceRadius,
    }}
  >
    {status.title}
  </Typography>
  )
}
