import { Box, Button, Typography } from "@material-ui/core";
import { navigate, RouteComponentProps } from "@reach/router";
import { Formik } from "formik";
import React, { ReactElement, useState } from "react";
import NiceBox from "../common/NiceBox";
import MobileInput from "../login/MobileInput";
import * as Yup from "yup";
import axios from "axios";
import PasswordResetForm from "./PasswordResetForm";
import { IApiReqPasswordReset } from "estreeh-common-library/src/interfaces/api/req/IApiReqPasswordReset";
import { IApiReqSendOtp } from "estreeh-common-library/src/interfaces/api/req/IApiReqSendOtp";
import { IApiResSendOtp } from "estreeh-common-library/src/interfaces/api/res/IApiResSendOtp";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { toast } from "react-toastify";

interface Props extends RouteComponentProps {}

export default function ForgotPasswordPage(props: Props): ReactElement {
  const [mobile, setMobile] = useState("");
  const [otpSent, setOtpSent] = useState(false);
  const [loading, setLoading] = useState(false);

  const initialPassForm: IApiReqPasswordReset = {
    mobile:`+973${mobile}`,
    password: "",
    confirmPassword: "",
    secret: "",
  };

  //TODO: Implement captcha

  return (
    <Box maxWidth={440} mx="auto" my={3}>
      <NiceBox>
        <Box my={4}>
          <Typography variant="h5" style={{ textAlign: "center" }}>
            Forgot Password
          </Typography>
        </Box>

        {otpSent === false && (
          <>
            <MobileInput
              value={mobile}
              onChange={(e) => {
                setMobile(e.target.value);
              }}
              isErrored={false}
              disabled={loading || otpSent}
            />
            <Box textAlign="center" my={2}>
              <Button
                color="primary"
                variant="contained"
                onClick={() => {
                  setLoading(true);
                  //TODO: Axios blah

                  const sendOtpBody: IApiReqSendOtp = {
                    to: `+973${mobile}`,
                    method: "sms",
                    purpose: "passwordReset",
                  };
                  axios
                    .post<IResponse<IApiResSendOtp>>(
                      process.env.REACT_APP_API_ROOT + "/otp/send",
                      sendOtpBody
                    )
                    .finally(() => {
                      setLoading(false);
                      setOtpSent(true);
                    });
                }}
                disabled={mobile.length < 8}
              >
                Send OTP
              </Button>
            </Box>
          </>
        )}
        {otpSent === true && (
          <Formik
            validationSchema={Yup.object({
              secret: Yup.string().required("Required"),
              password: Yup.string()
                .required("Required")
                .min(5, "Minimum 5 characters"),
              confirmPassword: Yup.string()
                .required("Required")
                .oneOf([Yup.ref("password"), null], "Passwords must match"),
            })}
            initialValues={initialPassForm}
            onSubmit={(values, { setSubmitting }) => {
              setSubmitting(true);

              const forgetBody: IApiReqPasswordReset = { ...values };

              axios
                .post<IResponse<boolean>>(
                  process.env.REACT_APP_API_ROOT + "/auth/reset-password",
                  forgetBody,
                  { withCredentials: true }
                )
                .then((result) => {
                  toast.success(result.data.message);
                  //TODO: Take him to a password success page
                  navigate("/");
                })
                .catch((reason) => {
                  toast.error(reason.response.data.message);
                })
                .finally(() => {
                  setSubmitting(false);
                });
            }}
          >
            {(formik) => <PasswordResetForm formik={formik} />}
          </Formik>
        )}
      </NiceBox>
    </Box>
  );
}
