import {
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
} from "@material-ui/core";
import React from "react";
const demoRadio = <Radio color="primary" />;

const RequestFrequency = () => {
  return (
    <FormControl component="fieldset">
      <FormLabel component="legend">
        Select how often do you require this service
      </FormLabel>
      <RadioGroup row>
      <FormControlLabel
          value="weekly"
          label="Every Week"
          control={demoRadio}
        />
        <FormControlLabel
          value="onetime"
          label="One Time"
          control={demoRadio}
        />

      </RadioGroup>
    </FormControl>
  );
};

export default RequestFrequency;
