import { useTranslation } from "react-i18next";
import { IFilterGroupContainer } from "estreeh-common-library/src/interfaces/IFilterGroupContainer";
// import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import FilterCheckboxItem from "./FilterCheckboxItem";
import FilterGroup from "./FilterGroup";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";

const FGNationalities = (props: IFilterGroupContainer) => {
  const { t } = useTranslation();
  const updateFiltersTwoYear = useStoreActions(
    (a) => a.updateFiltersTwoYear
  );
  const expanded = props.isMd
    ? props.expandedDefaultDesktop
    : props.expandedDefaultMobile;

  const nationalitiesState = useStoreState(
    (s) => s.filterOptions.nationality.items
  );
  const setNationalitiesState = useStoreActions(
    (s) => s.filterOptions.nationality.setItems
  );

  return (
    <FilterGroup
      title={t("nationalities")}
      expanded={expanded}
      hideDivider={props.hideDivider}
    >
      {nationalitiesState.map((nationality) => (
        <FilterCheckboxItem
          key={nationality.key}
          title={nationality.key}
          value={nationality.value}
          onChange={(e, checked) => {
            let s = nationalitiesState.slice(0);
            s.forEach((n) => {
              if (n.key === nationality.key) {
                n.value = checked;
              }
            });
            setNationalitiesState({ payload: s, setUnfiltered: false });
            let array = s.filter((n) => n.value === true).map((n) => n.key);
            updateFiltersTwoYear({
              key: "nationality",
              array: array,
            });
          }}
        />
      ))}
    </FilterGroup>
  );
};

export default FGNationalities;
