import { jobTitles, jobTitlesRentalForDemo } from "../../config/FilterOptions";
import NiceBox from "../common/NiceBox";
import FGAge from "./FGAge";
import FGEducation from "./FGEducation";
import FGExperience from "./FGExperience";
import FGJobTitles from "./FGJobTitles";
import FGLanguageSkills from "./FGLanguageSkills";
import FGLocalTransfer from "./FGLocalTransfer";
import FGMaritalStatus from "./FGMaritalStatus";
import FGNationalities from "./FGNationalities";
import FGProcessFees from "./FGProcessFees";
import FGSalary from "./FGSalary";

const SideFilter = (props: {
  is2Year: boolean;
  isLeased: boolean;
  isMd: boolean;
}) => {
  // const expanded = props.isMd ? undefined : false;

  // TODO: Make all filters functional (Client side filtering)

  return (
    <NiceBox>
      {props.is2Year && (
        <>
          <FGLocalTransfer hideDivider={false} expandedDefaultDesktop={true} expandedDefaultMobile={true} isMd={props.isMd} />
          <FGNationalities hideDivider={false} expandedDefaultDesktop={true} expandedDefaultMobile={false} isMd={props.isMd}  />
          <FGJobTitles jobTitles={jobTitles} expandedDefaultDesktop={true} expandedDefaultMobile={false} isMd={props.isMd}  />
          <FGSalary expandedDefaultDesktop={true} expandedDefaultMobile={false} isMd={props.isMd}  />
          <FGProcessFees expandedDefaultDesktop={true} expandedDefaultMobile={false} isMd={props.isMd}  />
          <FGLanguageSkills expandedDefaultDesktop={false} expandedDefaultMobile={false} isMd={props.isMd}   />
          <FGExperience expandedDefaultDesktop={false} expandedDefaultMobile={false} isMd={props.isMd}  />
          <FGEducation expandedDefaultDesktop={false} expandedDefaultMobile={false} isMd={props.isMd}  />
          <FGAge expandedDefaultDesktop={false} expandedDefaultMobile={false} isMd={props.isMd}  />
          <FGMaritalStatus expandedDefaultDesktop={false} expandedDefaultMobile={false} isMd={props.isMd}  />
        </>
      )}
      {props.isLeased && (
        <>
          {/* <FGService  expandedDefaultDesktop={true} expandedDefaultMobile={true} isMd={props.isMd} /> */}
          <FGJobTitles jobTitles={jobTitlesRentalForDemo} expandedDefaultDesktop={true} expandedDefaultMobile={true} isMd={props.isMd}  updatesRentalFilter={true} />
          <FGNationalities hideDivider={true} expandedDefaultDesktop={true} expandedDefaultMobile={true} isMd={props.isMd}  updatesRentalFilter={true} />
        </>
      )}
    </NiceBox>
  );
};

export default SideFilter;
