import { Icon } from "@material-ui/core";
import React from "react";
import Branding from "../../config/Branding";

const Dot = (props: { color?: string; px?: number; fontSize?:number }) => {
  const color = props.color !== undefined ? props.color : Branding.primary;
  const px = props.px !== undefined ? props.px : 6;
  const fontSize = props.fontSize ? props.fontSize : 8;
  return (
    <Icon
      fontSize="inherit"
      style={{
        paddingLeft: px,
        paddingRight: px,
        fontSize: fontSize,
        verticalAlign: "middle",
        color: color,
      }}
    >
      fiber_manual_record_icon
    </Icon>
  );
};

export default Dot;
