import { Box } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import { useStoreState } from "../../helpers/StoreTypedHooks";
import AgencyDetails from "./AgencyDetails";
import ScrollToTop from "../common/ScrollToTop";

interface Params {
  agencySlug: string;
  showTwoYearTabs: boolean;
  showRentalTabs: boolean;
  agencies: IAgency[];
}

interface Props extends RouteComponentProps<Params> {}

export default function AgencyPage(props: Props): ReactElement {
  const dataLoaded = useStoreState((state) => state.dataLoaded);

  let agency = null;
  let showTwoYearTabs = props.showTwoYearTabs ? props.showTwoYearTabs : false;
  let showRentalTabs = props.showRentalTabs ? props.showRentalTabs : false;

  if (props.agencySlug && props.agencies)
    if (props.agencies.length > 0) {
      const slug = props.agencySlug;
      agency = props.agencies?.filter((x) => x.slug === slug)[0];

      if (agency) {
        return (
          <React.Fragment>
            <ScrollToTop />
            <AgencyDetails
              agency={agency}
              showTwoYearTabs={showTwoYearTabs}
              showRentalTabs={showRentalTabs}
            />
          </React.Fragment>
        );
      }
    }
  return <Box>{dataLoaded ? <>Finding data..</> : <>Loading...</>}</Box>;
}
