import { Typography } from "@material-ui/core";
import { CSSProperties } from "@material-ui/styles";
import React, { ReactElement } from "react";

interface Props {
  title: string;
}

export default function LoginHeader({ title }: Props): ReactElement {
  const headStyle: CSSProperties = {
    // textAlign: "center",
  };

  const hVariant = "h5";
  return (
    <Typography variant={hVariant} style={headStyle}>
      {title}
    </Typography>
  );
}
