import { Box, Button, Grid, Typography } from "@material-ui/core";
import { Link, navigate, RouteComponentProps } from "@reach/router";
import axios from "axios";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import React, { ReactElement } from "react";
import { useStoreState } from "../../helpers/StoreTypedHooks";
import NiceBox from "../common/NiceBox";
import ScrollToTop from "../common/ScrollToTop";
import RequestApplicationDetails from "../request/RequestApplicationDetails";
import RequestPaymentSummary from "../request/RequestPaymentSummary";
import RequestWorkerContainer from "../request/RequestWorkerContainer";
import { IApiReqCancelRequest } from "estreeh-common-library/src/interfaces/api/req/IApiReqCancelRequest";
import { toast } from "react-toastify";
import AgencyInfoCard from "../agency/AgencyInfoCard";
import RequestStatusPill from "./RequestStatusPill";

interface Params {
  id: string;
}
interface Props extends RouteComponentProps<Params> {
  purpose: "cancel" | "view";
}

export default function RequestDetailsPage({
  purpose,
  id,
}: Props): ReactElement {
  const requests = useStoreState((s) => s.requests.items);
  const codes = useStoreState(s => s.config.item?.statusCodesTwoYear)
  const request = requests.find((x) => x.id === id);
  
  if (request === undefined || codes === undefined || request.status === undefined) {
    return (
      <>
        Request not found. Please try navigating from the{" "}
        <Link to="/">homepage</Link>
      </>
    );
  } else {
    const applicationForm = request?.applicationForm;
    const cancelMsg = request.status.allowCancel === true ? "" : " or to enquire about cancellation"

    const onClickCancel = () => {
      if (request._id !== undefined) {
        const url =
          process.env.REACT_APP_API_ROOT + "/customer/requests/cancel";
        const data: IApiReqCancelRequest = {
          requestId: request._id,
        };
        axios
          .put<IResponse<boolean>>(url, data, { withCredentials: true })
          .then((res) => {
            toast.success(res.data.message);
            navigate("/my/requests");
          })
          .catch((reason) => {
            toast.error(reason);
          });
      }
    };

    return (
      <Box>
        <ScrollToTop />
        <Box py={2}>
          <Typography variant="h4">
            Request Details: {request.workerTwoYear?.name}
          </Typography>
        </Box>
        <Box pb={2}>
          <RequestStatusPill status={request.status}/>
        </Box>
        <NiceBox p={4}>
          <Grid container spacing={1} direction="row-reverse">
            <Grid item xs={12} md={12}>
              <Box pb={2}>
                <Typography variant="h6">Agency Details</Typography>
              </Box>
              <Box pb={3}>
                For more details about your request{cancelMsg}, you may contact the agency:
              </Box>
              {request.workerTwoYear?.agency !== undefined && (
                <AgencyInfoCard agency={request.workerTwoYear?.agency} showContact={true} />
              )}
            </Grid>
            <Grid item xs={12} md={6}>
              <Box py={2}>
                <Typography variant="h6">Worker Details</Typography>
              </Box>
              <RequestWorkerContainer
                workerRental={request.workerRental}
                workerTwoYear={request.workerTwoYear}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              {/* <RequestScheduleDisplay applicationForm={applicationForm} /> */}
              <RequestApplicationDetails applicationForm={applicationForm} />
              <RequestPaymentSummary
                paymentItems={applicationForm?.paymentSummaryItems}
              />
            </Grid>
          </Grid>
          {purpose === "cancel" && (
            <Box py={2} my={3} textAlign="center">
              <Box
                pb={3}
                style={{
                  color: "red",
                }}
              >
                Are you sure you want to cancel this request? This cannot be
                undone.
              </Box>
              <Button
                variant="contained"
                color="primary"
                onClick={onClickCancel}
                style={{
                  background: "darkred",
                }}
              >
                CANCEL THIS REQUEST
              </Button>
            </Box>
          )}
        </NiceBox>
      </Box>
    );
  }
}
