/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import { Box } from "@material-ui/core";
import { Link } from "@reach/router";
import React, { ReactElement } from "react";
import LogoImg from "../../images/logo.svg";

interface Props {
  marginLeft: number;
  marginRight?: number;
  maxHeight: number;
}

export default function Logo(props: Props): ReactElement {
  return (
    <Box
      css={{
        outline: "none",
        border:0,
        ":focus": {
          outline: "none",
        },
      }}
    >
      <Link
        to="/"
        css={{
          textDecoration: "none",
          display: "inline-block",
          verticalAlign: "top",
          marginLeft: props.marginLeft,
          marginRight: props.marginRight,
          border:0,
          outline: "none",
          ":focus": {
            outline: "none",
          },
        }}
      >
        <img
          src={LogoImg}
          css={{
            display: "block",
            maxHeight: props.maxHeight,
            maxWidth: "100%",
            border:0,
            outline: "none",
            ":focus": {
              outline: "none",
            },
          }}
          alt="logo"
        />
      </Link>
    </Box>
  );
}
