import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import IReview from "estreeh-common-library/src/interfaces/IReview";
import _ from "lodash";
import Faker from "faker";
import {
  // demoAgencies,
  demoRentalMonthlyRemarks,
  demoRentalHourlyRemarks,
  // demoNumberOfContractWorkersPerAgency,
} from "../config/DemoItems";
// import { TransformTwoYearWorker } from "./TransformTwoYearWorker";
// import { MakeTabsTwoYearWorkers } from "./MakeTabsTwoYearWorkers";
import { MakeTabsServices } from "./MakeTabsServices";
import { MakeTabsRentalWorkers } from "./MakeTabsRentalWorkers";
import { TransformRentalWorker } from "./TransformRentalWorker";
import { TranslationPack } from "estreeh-common-library/src/types/TranslationPack";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import IWorkerRental from "estreeh-common-library/src/interfaces/IWorkerRental";
import { CleanAgency } from "./CleanAgency";
import { AssignTwoYearWorkersToAgency } from "./AssignTwoYearWorkersToAgency";
import { S3Folder } from "estreeh-common-library/src/types/S3Folder";

export const TransformAgency = (
  agency: IAgency,
  index: number,
  translate: TranslationPack,
  allWorkers: {
    twoYear?: IWorkerTwoYear[];
    rental?: IWorkerRental[];
  }
) => {
  CleanAgency(agency);
  let tabsIndex = 0;

  agency.rating = calcRatings(agency.reviews);

  agency.showReviewsRatings = agency.rating >= 3;

  if (agency.servicesSelectableWorkers) {
    agency.servicesSelectableWorkers.forEach((service) => {
      if (service.isTwoYearContract) {
        agency.hasTwoYearWorkerServices = true;
      }
      if (
        service.isRentalHourly ||
        service.isRentalMonthly ||
        service.isRentalMonthlyStayOver
      ) {
        agency.hasRentedServices = true;
        service.workersRental = _.orderBy(
          service.workersRental,
          ["rating"],
          ["desc"]
        );
      }
      if (service.isRentalHourly) {
        service.remarks = Faker.random.arrayElement(demoRentalHourlyRemarks);
      }
      if (service.isRentalMonthly) {
        service.remarks = Faker.random.arrayElement(demoRentalMonthlyRemarks);
      }
    });
  }

  // agency.nationalities?.forEach((nationality) =>
  //   TransformNationalities(agency, nationality)
  // );

  tabsIndex = AssignTwoYearWorkersToAgency(
    agency,
    allWorkers.twoYear,
    tabsIndex
  );

  if (agency.hasRentedServices) {
    agency.servicesSelectableWorkers?.forEach((service) => {
      service.workersRental.forEach((worker) => {
        TransformRentalWorker(worker, service, agency, translate);
      });
    });
    tabsIndex = MakeTabsRentalWorkers(agency, tabsIndex);
  }

  if (agency.services) {
    MakeTabsServices(agency, tabsIndex);
  }
  TransformAgencyLogo(agency);
};

export const TransformAgencyLogo = (agency:IAgency) => {
  const folder: S3Folder = "agency/sm";
  agency.logo = "/" + folder + "/" + agency._id + "." + agency.logoFileExt;
}

const average = (arr: Array<number>) =>
  arr.reduce((p, c) => p + c, 0) / arr.length;

const calcRatings = (reviews?: IReview[]) => {
  if (reviews === undefined) return 0;
  if (reviews.length === 0) return 0;

  const ratingsArray = reviews.map((review) => {
    let rRatings = [];
    if (review.agencyRating) rRatings.push(review.agencyRating);
    if (review.workerRating) rRatings.push(review.workerRating);

    return average(rRatings);
  });

  return average(ratingsArray);
};
