import {  Box, Divider, Grid, Typography } from "@material-ui/core";
import React from "react";
import { useTranslation } from "react-i18next";
import Branding from "../../../config/Branding";
import { localTransferExplanation } from "../../../config/FilterOptions";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import DetailsButton from "../../common/DetailsButton";
import FavoriteButton from "../../common/FavoriteButton";
import NiceBox from "../../common/NiceBox";
// import OrangeChip from "../../common/OrangeChip";
import SelectWorkerButton from "../../common/SelectWorkerButton";
import IconInfo from "../IconInfo";
import Worker2SmallPic from "./Worker2SmallPic";

const Worker2GridCard = (props: {
  worker: IWorkerTwoYear;
  picClickEvent: any;
  onReserveButton: React.MouseEventHandler<HTMLButtonElement>;
}) => {
  const {t} = useTranslation();

  const worker = props.worker;

  const clickableStyle: React.CSSProperties = {
    cursor: "pointer",
    userSelect: "none",
  };


  // const localTransferChip = worker.isLocalTransfer ? (
  //   <OrangeChip label="Local Transfer" />
  // ) : null;

  const localTransferInfoLine = (
    <React.Fragment>
      {worker.isLocalTransfer && (
        <IconInfo style={{ color: Branding.text.warning }} icon="warning" title={localTransferExplanation}>
          Available in Bahrain
        </IconInfo>
      )}
      {!worker.isLocalTransfer && <IconInfo icon="hehehehe">&nbsp;</IconInfo>}
    </React.Fragment>
  );

  // const localTransferGrid = worker.isLocalTransfer ? (
  //   <Grid xs={6}>{localTransferInfoLine}</Grid>
  // ) : null;

  return (
    <Grid item xs={12} sm={6} md={4}>
      <NiceBox p={1} my={0}>
        <Box style={clickableStyle} onClick={props.picClickEvent}>
          <Grid container>
            <Grid item xs={worker.isLocalTransfer ? 12 : 12}>
              <Typography style={{ fontWeight: "bold" }}>
                {worker.name}
              </Typography>
            </Grid>
          </Grid>
          <Box py={1}>
            <Divider />
          </Box>

          <Box>
            <Grid container>
              <Grid item xs={5}>
                <Box textAlign="center" style={clickableStyle}>
                  <Worker2SmallPic worker={worker} variant={1} />
                </Box>
              </Grid>
              <Grid item xs={7}>
                <Box px={1}>
                  <IconInfo icon="work" title="Job Title">
                    {worker.jobTitle}
                  </IconInfo>
                  <IconInfo icon="history" title="Age">
                    {worker.age} {t("years-old")}
                  </IconInfo>
                  <IconInfo icon="brightness_3" title="Religion">
                    {worker.religion}
                  </IconInfo>
                  <IconInfo icon="fiber_smart_record" title="Marital Status">
                    {worker.maritalStatus}
                  </IconInfo>
                  <IconInfo icon="local_airport" title="Experience">
                    {worker.experienceYears} years experience
                  </IconInfo>
                  <IconInfo icon="attach_money" title="Salary">
                    {worker.monthlySalaryBhdFormatted}
                  </IconInfo>
                  <IconInfo icon="request_quote" title="Process Fees">
                    {worker.processFeesBhdFormatted}
                  </IconInfo>
                  {localTransferInfoLine}
                </Box>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Box py={1}>
          <Divider />
        </Box>
        <Box textAlign="center">
          <FavoriteButton worker={worker} />
          <DetailsButton onClick={props.picClickEvent} />
          <SelectWorkerButton onClick={props.onReserveButton} />
        </Box>
      </NiceBox>
    </Grid>
  );
};

export default Worker2GridCard;
