import { Avatar, Box, Divider, Grid, Typography } from "@material-ui/core";
import React from "react";
import Branding from "../../../config/Branding";
import UseStyles from "../../../config/UseStyles";
import IWorkerRental from "estreeh-common-library/src/interfaces/IWorkerRental";
import Dot from "../../common/Dot";
import FavoriteButton from "../../common/FavoriteButton";
import ReserveWorkerButton from "../../common/ReserveWorkerButton";
import SmallText from "../../common/SmallText";
import WorkerRentalCardSlots from "./WorkerRentalCardSlots";

const WorkerRentalListCard = (props: {
  worker: IWorkerRental;
  isHourly: boolean;
  isMonthly: boolean;
  picHeight?: number;
  showButtons?: boolean;
}) => {
  const worker = props.worker;
  const classes = UseStyles();
  const clickableStyle = false ? { cursor: "pointer" } : {};

  const picWidth = "100%";
  const picHeight = props.picHeight ? props.picHeight : 80;
  const hourlyWorkerLink = "/new-request/hourly-worker";
  const monthlyWorkerLink = "/new-request/monthly-worker";

  const buttons = props.showButtons ? (
    <Grid item xs={5}>
      <Box textAlign="right" px={1} color={Branding.text.secondary}>
        <FavoriteButton />
        <ReserveWorkerButton
          link={props.isHourly ? hourlyWorkerLink : monthlyWorkerLink}
        />
      </Box>
    </Grid>
  ) : null;

  return (
    <React.Fragment key={worker._id}>
      <Box pt={1} className={classes.hoverBox}>
        <Grid container spacing={2}>
          <Grid item xs={2}>
            <Box textAlign="center" style={clickableStyle}>
              <Avatar
                src={worker.smallPicture}
                style={{
                  width: picWidth,
                  minHeight: picHeight,
                  alignSelf: "center",
                }}
                variant="rounded"
              />
            </Box>
          </Grid>
          <Grid item xs={10}>
            <Grid container>
              <Grid item xs={7}>
                <Box pb={1} style={clickableStyle}>
                  <Typography style={{ fontWeight: "bold" }}>
                    {worker.name}
                  </Typography>
                </Box>
              </Grid>
              {buttons}
            </Grid>
            <Box>
              <Grid container>
                <Grid item xs={12}>
                  <SmallText>
                    {worker.nationality}
                    <Dot />
                    {worker.jobTitle}
                    <Dot />
                    {worker.feesText}
                  </SmallText>
                </Grid>
                {worker.remarks && (
                  <Grid item xs={12}>
                    <SmallText>{worker.remarks}</SmallText>
                  </Grid>
                )}
              </Grid>
            </Box>
            <SmallText>
              You can select more than 1 {props.isHourly ? "timing" : "month"}:
            </SmallText>
            <WorkerRentalCardSlots
              worker={worker}
              isHourly={props.isHourly}
              isMonthly={props.isMonthly}
              md={2}
              maxDays={6}
              maxMonths={6}
            />
          </Grid>
        </Grid>
        <Box pt={1}>
          <Divider />
        </Box>
      </Box>
    </React.Fragment>
  );
};

export default WorkerRentalListCard;
