import { Box } from "@material-ui/core";
import { CSSProperties } from "@material-ui/core/styles/withStyles";
import React from "react";

interface INiceBox {
  p?: number;
  my?: number;
  children: React.ReactNode;
  style?: CSSProperties;
}

const NiceBox = (props: INiceBox) => {
  const p = props.p === undefined ? 2 : props.p;
  const my = props.my === undefined ? 1 : props.my;
  return (
    <Box
      bgcolor="white"
      p={p}
      my={my}
      boxShadow="0 0 25px #00000011"
      borderRadius={10}
      style={props.style}
    >
      {props.children}
    </Box>
  );
};

export default NiceBox;
