import { Box, Grid } from "@material-ui/core";
import { HomeTwoTone } from "@material-ui/icons";
import React, { ReactElement } from "react";
import Branding from "../../config/Branding";
// import Branding from "estreeh-common-library/src/config/Branding";
import SmallText from "../common/SmallText";

interface Props {
  text?: string;
}

export default function AgencyTabRemarks(props: Props): ReactElement {
  const iconStyles: React.CSSProperties = {
    fontSize: 19,
    color: Branding.primary,
    verticalAlign: "text-bottom",
  };

  return (
    <React.Fragment>
      {props.text && (
        <Box
          boxShadow={Branding.shadow.niceShadow}
          p={1}
          my={1}
          bgcolor={Branding.lightest}
          borderRadius={Branding.borderRadius.niceRadius}
        >
          <SmallText>
            <Grid container spacing={1} alignItems="center">
              <Grid item>
                <HomeTwoTone style={iconStyles} />
              </Grid>
              <Grid item>
                <span dangerouslySetInnerHTML={{ __html: props.text }}></span>
              </Grid>
            </Grid>
          </SmallText>
        </Box>
      )}
    </React.Fragment>
  );
}
