import React from 'react';
import { useTranslation } from 'react-i18next';
import { maxExperience, minExperience } from '../../config/FilterOptions';
import { IFilterGroupContainer } from 'estreeh-common-library/src/interfaces/IFilterGroupContainer';
import FilterGroup from './FilterGroup';
import FilterSlider from './FilterSlider';

const FGExperience = (props:IFilterGroupContainer) => {
  const {t} = useTranslation();
  // const expanded = props.expanded !== undefined ? props.expanded : false;
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title={t("experience")} expanded={expanded}>
    <FilterSlider
        defaultValue={[minExperience, maxExperience]}
        min={minExperience}
        max={maxExperience}
        step={1}
        isMd={props.isMd}
      />
    </FilterGroup>
  );
};

export default FGExperience;