import { createStyles, makeStyles, Theme } from "@material-ui/core";
import Branding from "./Branding";

const wizBlur = "blur(10px)";
// const wizBg = `linear-gradient(to top,${Branding.lightest}99, #ffffff00)`;
const wizBg = `#ffffff`;
const wizBorder = 3;

const UseStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      // backgroundColor: Branding.primary,
      "& a": {
        color: Branding.primary,
      },
      "& .material-icons": {
        verticalAlign: "text-bottom",
      },

      "& .transitionDefault": {
        transition: Branding.transition,
      },
      "& .MuiTextField-root": {
        marginTop: theme.spacing(0.5),
        marginBottom: theme.spacing(0.5),
        width: "100%",
      },
      "& .MuiAvatar-img": {
        objectFit: "contain",
      },
      "& .no-flex-direction":{
        flexDirection:"initial",
      },
      "& .notched-outline-no-border":{
        borderTop:0,
        borderRight:0,
        borderBottom:0
      }
    },
    objectFitContain:{
      "& .MuiAvatar-img": {
        objectFit: "contain",
      },
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: "center",
      color: theme.palette.text.primary,
      // boxShadow: "0 0 3px #000",
    },

    transitionDefault: {
      transition: Branding.transition,
    },

    wizardCardLink: {
      display: "block",
      borderRadius: 10,
      boxShadow: "0 0 22px #00000022",
      border:`${wizBorder}px solid ${Branding.primary}`,
      background: wizBg,
      backdropFilter:wizBlur,
      textDecoration: "none",
      transition: Branding.transition,
      "&:hover": {
        background: Branding.lightest,
        boxShadow: "0 0 2px " + Branding.primary,
      },
    },
    wizardCardLinkDisabled:{
      display: "block",
      // backdropFilter:wizBlur,
      // zIndex:-20,
      fontSize: 8,
      border:`${wizBorder}px solid #00000033`,
      borderRadius: 10,
      boxShadow: "0 0 22px #00000022",
      background: "#ffffff",
      textDecoration: "none",
      transition: Branding.transition,
      "&:hover": {
        background: Branding.lightBg,
        boxShadow: "0 0 2px #00000066",
      },
    },

    hoverBox: {
      transition: Branding.transition,
      userSelect: "none",
      "&:hover": {
        backgroundColor: Branding.lightBg,
      },
    },
  })
);

export default UseStyles;
