import { Box, Grid } from "@material-ui/core";
import React from "react";
import FormatCurrency from "../../helpers/FormatCurrency";
import SmallText from "./SmallText";

const PaymentSummaryItem = (props: {
  title: string;
  amount: number;
  emphasized?: boolean;
  py?: number;
}) => {
  const formattedAmount = FormatCurrency(props.amount);

  const fontWeight = props.emphasized ? "bold" : "normal";
  const py = props.py ? props.py : 0;
  return (
    <Box fontWeight={fontWeight} py={py}>
      <Grid container spacing={1}>
        <Grid item xs={8}>
          <SmallText style={{ fontWeight: fontWeight }}>
            {props.title}
          </SmallText>
        </Grid>
        <Grid item xs={4}>
          <Box textAlign="left">
            <SmallText style={{ fontWeight: fontWeight }}>
              <span style={{ fontSize:"80%" }}>BHD</span> {formattedAmount}
            </SmallText>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
};

export default PaymentSummaryItem;
