function FormatCurrency(number: number, fixed: number = 3) {
  if (number !== undefined) {
    if (fixed > 0) {
      return number.toFixed(fixed).replace(/\d(?=(\d{3})+\.)/g, "$&,");
    } else {
      return number.toFixed(fixed).replace(/\d(?=(\d{3})+)/g, "$&,");
    }
  }
}

export default FormatCurrency;
