import React from 'react';
import { useTranslation } from 'react-i18next';
import { maxSalary, minSalary } from '../../config/FilterOptions';
import { IFilterGroupContainer } from 'estreeh-common-library/src/interfaces/IFilterGroupContainer';
import FilterGroup from './FilterGroup';
import FilterSlider from './FilterSlider';

const FGSalary = (props:IFilterGroupContainer) => {
  const {t} = useTranslation();
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title={t('salary-bd')} expanded={expanded}>
    <FilterSlider
      defaultValue={[minSalary, maxSalary]}
      min={minSalary}
      max={maxSalary}
      step={10}
      isMd={props.isMd}
    />
  </FilterGroup>
  );
};

export default FGSalary;