import React from "react";
import IAgencyTab from "estreeh-common-library/src/interfaces/IAgencyTab";
import WorkerRentalListCard from "../worker/rental/WorkerRentalListCard";
import Worker2CardWrapper from "../worker/two-years/Worker2CardWrapper";

const AgencyTabPanelListDesign = (props: {
  tab: IAgencyTab;
  isHourly: boolean;
  isMonthly: boolean;
}) => {
  const tab = props.tab;
  return (
    <>
      {tab.workersTwoYear?.map((worker) => (
        <Worker2CardWrapper worker={worker} design="list" />
      ))}
      {tab.workersRental?.map((worker) => (
        <WorkerRentalListCard
          worker={worker}
          isHourly={props.isHourly}
          isMonthly={props.isMonthly}
          showButtons={true}
        />
      ))}
    </>
  );
};

export default AgencyTabPanelListDesign;
