import {
  Box,
  Checkbox,
  FormControlLabel,
  OutlinedInputProps,
  TextField,
  Typography,
} from "@material-ui/core";
import React from "react";
// import { ApplicationFormSetter } from "estreeh-common-library/src/types/ApplicationFormSetter";
import { IApplicationForm } from "estreeh-common-library/src/interfaces/IApplicationForm";
import { FormikProps } from "formik";

const RequestFormGeneral = (props: {
  showAddress?: boolean;
  showFrequency?: boolean;
  showEquipmentOption?: boolean;
  // applicationForm: IApplicationForm;
  // setApplicationForm: ApplicationFormSetter;
  formik: FormikProps<IApplicationForm>;
}) => {
  const headerVariant = "h6";
  const inputSize: "small" | "medium" = "medium";
  const inputVariant = "outlined";
  const showAddress = props.showAddress ? props.showAddress : false;
  const showEquipmentOption = props.showEquipmentOption
    ? props.showEquipmentOption
    : false;

  const formik = props.formik;

  const InputProps:Partial<OutlinedInputProps> = {
    readOnly: true,
    classes: {
      notchedOutline: "notched-outline-no-border",
    },
  }

  // const a = props.applicationForm;
  // const set = props.setApplicationForm;

  //TODO: Two year contract some have addons options (like INSURANCE) F#36
  //TODO: Implement FORM VALIDATION (Research from tutorials and videos)
  //TODO: Implement check for duplicate email That is VALIDATED email (Allow duplicate unvalidated email)
  //TODO: Bind these fields (it works or not..?)

  // TODO: Mobile Number is mandatory
  // TODO: If logged in, mobile number is read-only

  // TODO: New page after submit IF NOT LOGGED IN, check via API if mobile number already exist or not:
  // TODO: If not logged in + Mobile not exist: OTP
  // TODO: If not logged in + Mobile exist: Enter Password
  // TODO: If logged in: Skip

  return (
    <>
      <Box pb={1}>
        <Typography variant={headerVariant}>Application Details</Typography>
      </Box>
      <Box pb={1}>
        <TextField
          label="Name"
          variant="outlined"
          InputProps={InputProps}
          size={inputSize}
          error={
            formik.touched.fullName && formik.errors.fullName !== undefined
          }
          helperText={formik.touched.fullName && formik.errors.fullName}
          {...formik.getFieldProps("fullName")}
        />
        <TextField
          label="Mobile Number"
          variant="outlined"
          InputProps={InputProps}
          size={inputSize}
          error={
            formik.touched.mobileNumber &&
            formik.errors.mobileNumber !== undefined
          }
          helperText={formik.touched.mobileNumber && formik.errors.mobileNumber}
          {...formik.getFieldProps("mobileNumber")}
        />
        <TextField
          label="Other Mobile Number (Optional)"
          variant={inputVariant}
          size={inputSize}
          error={
            formik.touched.otherMobileNumber &&
            formik.errors.otherMobileNumber !== undefined
          }
          helperText={
            formik.touched.otherMobileNumber && formik.errors.otherMobileNumber
          }
          {...formik.getFieldProps("otherMobileNumber")}
          // value={a.otherMobileNumber}
          // onChange={(e) =>
          //   set((v) => ({ ...v, otherMobileNumber: e.target.value }))
          // }
        />
        {/* <TextField
          label="Email"
          variant={inputVariant}
          size={inputSize}
          // type="email"
          error={formik.touched.email && formik.errors.email !== undefined}
          helperText={formik.touched.email && formik.errors.email}
          {...formik.getFieldProps("email")}
          // value={a.email}
          // onChange={(e) => set((v) => ({ ...v, email: e.target.value }))}
        /> */}
        {showAddress && (
          <TextField
            label="Address"
            variant={inputVariant}
            size={inputSize}
            multiline
            rows={3}
            {...formik.getFieldProps("address")}
            // value={a.address}
            // onChange={(e) => set((v) => ({ ...v, address: e.target.value }))}
          />
        )}
        <TextField
          label="Special Notes (Optional)"
          variant={inputVariant}
          size={inputSize}
          multiline
          rows={3}
          {...formik.getFieldProps("specialNotes")}
          // value={a.specialNotes}
          // onChange={(e) => set((v) => ({ ...v, specialNotes: e.target.value }))}
        />
        {showEquipmentOption && (
          <FormControlLabel
            label="Bring Equipment (2 BD/Day)"
            control={
              <Checkbox
                color="primary"
                {...formik.getFieldProps("bringCleaningTools")}
              />
            }
          />
        )}
      </Box>
    </>
  );
};

export default RequestFormGeneral;
