import FormatCurrency from "../helpers/FormatCurrency";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import { TranslationPack } from "estreeh-common-library/src/types/TranslationPack";
import { S3Folder } from "estreeh-common-library/src/types/S3Folder";
import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import { TransformAgencyLogo } from "./TransformAgency";

export function TransformTwoYearWorker(
  worker: IWorkerTwoYear,
  tPack: TranslationPack,
  agencies?: IAgency[]
) {
  let grandTotalYears = 0;
  let currency = tPack.t("currency.bhd");

  if (worker.jobExperiences.length > 0) {
    worker.jobExperiences.forEach((job) => {
      job.toYear = job.fromYear + job.totalYears;
      grandTotalYears += job.totalYears;
    });
    worker.experienceYears = grandTotalYears;
  }
  worker.monthlySalaryBhdFormatted =
    currency + " " + FormatCurrency(worker.monthlySalary, 0) + " / Month";
  worker.processFeesBhdFormatted =
    currency + " " + FormatCurrency(worker.processFees, 0);

  const folderSm: S3Folder = "2y/sm";
  const folderLg: S3Folder = "2y/lg";
  worker.smallPicture =
    "/" + folderSm + "/" + worker._id + "." + worker.smallPictureFileExt;
  worker.bigPicture =
    "/" + folderLg + "/" + worker._id + "." + worker.bigPictureFileExt;

  const minAge = Math.floor(worker.age / 5) * 5;
  worker.ageApprox = `${minAge}-${minAge + 5}`;

  if (agencies !== undefined && worker.agencyId !== undefined) {
    let agency = agencies.find((x) => x._id === worker.agencyId);
    if (agency !== undefined) {
      TransformAgencyLogo(agency);
      worker.agency = agency;
    }
  }

  // if (agency.nationalities) {
  //   worker.nationality.title = Faker.random.arrayElement(
  //     agency.nationalities
  //   ).title;
  // let demoId = getDemoId(worker.nationality.title);
  // worker.smallPicture = getDemoPic(demoId, worker.nationality.title, "Small");
  // worker.bigPicture = getDemoPic(demoId, worker.nationality.title, "Full");
  // }
}
