import { Box } from "@material-ui/core";
import Branding from "../../config/Branding";
import SiteHeadLarge from "./SiteHeadLarge";
import LandingPage from "../pages/LandingPage";
import { withBreakpoints } from "react-breakpoints";
import CookieConsent from "react-cookie-consent";
import Footer2 from "../common/Footer2";
import Footer1 from "../common/Footer1";

const FrontPageLayout = (props: {
  classes: any;
  breakpoints: any;
  currentBreakpoint: any;
}) => {
  const { breakpoints, currentBreakpoint } = props;
  const isMd = breakpoints[currentBreakpoint] >= breakpoints.md;
  const burgerWidth = 26;

  return (
    <Box bgcolor={Branding.lightBg}>
      <CookieConsent>
        This website uses cookies to enhance the user experience.
      </CookieConsent>
      <SiteHeadLarge
        isMd={isMd}
        burgerWidth={burgerWidth}
      />
      <Box bgcolor={Branding.lightBg} id="page-content-container" style={{ height: "100%" }}>
        <LandingPage isMd={isMd} />
        <Footer1/>
        <Footer2/>
      </Box>
    </Box>
  );
};

export default withBreakpoints(FrontPageLayout);
