import { Box, Grid, Typography } from "@material-ui/core";
import React, { ReactElement } from "react";
import { IApplicationForm } from "estreeh-common-library/src/interfaces/IApplicationForm";
import LabelValueLine from "../common/LabelValueLine";

interface Props {
  selectedHours?: {
    key: string;
    value: string;
  }[];
  selectedHoursFrequency?: string;
  selectedMonths?: string[];
  applicationForm?: IApplicationForm;
}

export default function RequestScheduleDisplay(props: Props): ReactElement {
  const oneLine = true;
  let selectedHours;
  let selectedMonths;
  let selectedHoursFrequency: string | undefined;

  if (props.applicationForm !== undefined) {
    selectedHours = props.applicationForm.selectedHours;
    selectedMonths = props.applicationForm.selectedMonths;
    selectedHoursFrequency = props.applicationForm.selectedHoursFrequency;
  } else {
    selectedHours = props.selectedHours;
    selectedMonths = props.selectedMonths;
    selectedHoursFrequency = props.selectedHoursFrequency;
  }
  if (selectedHours === undefined && selectedMonths === undefined) {
    return <Box></Box>;
  }
  return (
    <Box>
      <Box pb={1}>
        <Typography variant="h6">Selected Schedule</Typography>
      </Box>
      <Grid container>
        {selectedHours &&
          selectedHours.map((selectedHour) => (
            <LabelValueLine title={selectedHour.key} oneLine={oneLine}>
              {selectedHour.value}
            </LabelValueLine>
          ))}
        {selectedHours !== undefined && selectedHours.length > 0 && (
          <LabelValueLine title="Frequency" oneLine={oneLine}>
            {selectedHoursFrequency}
          </LabelValueLine>
        )}
        {selectedMonths !== undefined && (
          <LabelValueLine title="Months" oneLine={oneLine}>
            {selectedMonths.join(", ")}
          </LabelValueLine>
        )}
      </Grid>
    </Box>
  );
}
