import Branding from "../config/Branding";
import HourlySlotFactory from "../factories/HourlySlotFactory";
import { DaysArray, MonthsArray } from "../helpers/DateArrays";
import IHourlySlot from "estreeh-common-library/src/interfaces/IHourlySlot";
import IWorkerRental from "estreeh-common-library/src/interfaces/IWorkerRental";
import dateFormat from "dateformat";
import IMonthlySlot from "estreeh-common-library/src/interfaces/IMonthlySlot";
import MonthlySlotFactory from "../factories/MonthlySlotFactory";
import Base_ISlot from "estreeh-common-library/src/interfaces/Base_ISlot";
import { instanceOfIAgency } from "../interface_tests/interfaceTests";

export function MakeHourlySlots(worker: IWorkerRental): IHourlySlot[] {
  let slots: IHourlySlot[] = [];
  const agency = instanceOfIAgency(worker.agency) ? worker.agency : undefined;
  let days = DaysArray(agency?.hourlySlotsGenerateDaysInAdvance);

  days.forEach((day) => {
    worker.hourlySlotDefinitions.forEach((definition) => {
      let slot: IHourlySlot = HourlySlotFactory.build({
        date: day,
        worker: worker,
        hourlySlotDefinition: definition,
        dateDisplay: dateFormat(day, "d-mmm"),
        dateFull: dateFormat(day, "yyyy-mm-dd"),
        dayDisplay: dateFormat(day, "ddd"),
      });
      SetSlot(slot);
      slots.push(slot);
    });
  });

  return slots;
}



export function MakeMonthlySlots(worker: IWorkerRental): IMonthlySlot[] {
  let months = MonthsArray();
  let slots: IMonthlySlot[] = [];

  months.forEach((month) => {
    let slot: IMonthlySlot = MonthlySlotFactory.build({
      date: month,
      worker: worker,
    });
    SetSlot(slot);
    slots.push(slot);
  });

  return slots;
}

function SetSlot(slot: Base_ISlot) {
  const slotSettings = {
    Free: {
      key: "Free",
      displayText: "Free",
      color: Branding.slotColors.free,
      colorDot: Branding.slotColorsDot.free,
      clickable: true,
      cursor: "pointer",
    },
    Booked: {
      key: "Booked",
      displayText: "Booked",
      color: Branding.slotColors.booked,
      colorDot: Branding.slotColorsDot.booked,
      clickable: false,
      cursor: "default",
    },
    Off: {
      key: "Off",
      displayText: "Off",
      color: Branding.slotColors.off,
      colorDot: Branding.slotColorsDot.off,
      clickable: false,
      cursor: "default",
    },
  };
  
  let setting = slotSettings.Off;

  if (slot.availabilityStatus === "Free") {
    setting = slotSettings.Free;
  }
  if (slot.availabilityStatus === "Booked") {
    setting = slotSettings.Booked;
  }

  slot.color = setting.color;
  slot.colorDot = setting.colorDot;
  slot.availabilityDisplayText = setting.displayText;
  slot.clickable = setting.clickable;
}
