import { Box, Grid } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement, useState } from "react";
import { useTranslation } from "react-i18next";
import { INavItem } from "estreeh-common-library/src/interfaces/INavItem";
import LeftNavItem from "../common/LeftNavItem";
import LogoutDialog from "../common/LogoutDialog";
import { useStoreState } from "../../helpers/StoreTypedHooks";

interface Props extends RouteComponentProps {
  children: React.ReactNode;
}

export default function MyLayout(props: Props): ReactElement {
  const { t } = useTranslation();
  const [logoutDialogOpen, setLogoutDialogOpen] = useState(false);
  const user = useStoreState((s) => s.user.item);

  const back = {
    title: "Back",
    to: "/",
  };

  const myRequests = {
    title: "My Requests",
    to: "requests",
  };

  const myFavorites = {
    title: "My Favorites",
    to: "favorites",
  };

  const login = {
    title: "Login / Sign-up",
    to: "/login",
  };
  const profile = {
    title: "Profile",
    to: "profile",
  };
  const changePassword = {
    title: "Change Password",
    to: "password",
  };
  const logout = {
    title: t("Logout"),
    onClick: () => {
      setLogoutDialogOpen(true);
    },
  };

  const menu: INavItem[] =
    user === undefined
      ? [back, myFavorites, login]
      : [back, myRequests, myFavorites, profile, changePassword, logout];
  return (
    <>
      <LogoutDialog open={logoutDialogOpen} setOpen={setLogoutDialogOpen} />
      <Grid container spacing={0}>
        <Grid item xs={2}>
          <Box mx={2} my={4}>
            {menu.map((nav) => (
              <LeftNavItem navItem={nav} my={1} mx={0} key={nav.title} />
            ))}
          </Box>
        </Grid>
        <Grid item xs={10}>
          <Box minHeight={400}>{props.children}</Box>
        </Grid>
      </Grid>
    </>
  );
}
