import { Link } from "@reach/router";
import React from "react";
import { useTranslation } from "react-i18next";
import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import RatingHtml from "../common/RatingHtml";
import SmallText from "../common/SmallText";

const AgencyRatingInfo = (props: { agency: IAgency }) => {
  const ratingsCount = props.agency.reviews?.length;
  const {t} = useTranslation();
  // const reviewsCount = props.agency.reviews?.filter((review) => {
  //   if (review.text) return review.text?.length > 0;
  //   return false;
  // }).length;
  return (
    <SmallText>
      <RatingHtml rating={props.agency.rating} />
      {props.agency.showReviewsRatings && (
        <React.Fragment>
          {/* <span> (Based on {ratingsCount} ratings)</span> */}
          <span> - </span>
          {/* <Link to="#">{t("reviews")} ({reviewsCount})</Link> */}
          <Link to="#">{t("ratings")} ({ratingsCount})</Link>
        </React.Fragment>
      )}
    </SmallText>
  );
};

export default AgencyRatingInfo;
