/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import { Box, Icon, Typography } from "@material-ui/core";
import { useState } from "react";
import Branding from "../../../config/Branding";
import IHourlySlot from "estreeh-common-library/src/interfaces/IHourlySlot";
import dateFormat from "dateformat";

const HourlySlot = (props: { slot: IHourlySlot }) => {
  const slot = props.slot;
  const [selected, setSelected] = useState(slot.selected);
  const from = dateFormat(slot.hourlySlotDefinition?.from, "h:MM");
  const to = dateFormat(slot.hourlySlotDefinition?.to, "h:MM tt");
  // const single = dateFormat(slot.hourlySlotDefinition?.from, "h:MM tt");
  // const selectedColor = "#56c7ff";
  const fontSize = Branding.fontSize.small - 2;
  const selectedColor = Branding.slotColors.selected;
  const shadow = Branding.slotColors.shadow(slot.color);
  const shadowSelected = Branding.slotColors.shadow(selectedColor, 2, 1);
  // const hoverStyles: CSSObject = {
  //   backgroundColor: Branding.slotColors.hover,
  //   boxShadow: Branding.slotColors.shadow(Branding.primary, 0, 1),
  // };
  return (
    <div
      onClick={() => {
        if (slot.clickable) setSelected(!selected);
      }}
      css={{
        cursor: slot.clickable ? "pointer" : "default",
        padding: "2px 4px",
        margin: "6px 0",
        backgroundColor: selected ? selectedColor : slot.color,
        borderRadius: Branding.borderRadius.niceRadius / 2,
        boxShadow: selected ? shadowSelected : shadow,
        transition: Branding.transition,
        userSelect: "none",
        position: "relative",
        // ":hover": slot.clickable && !selected ? hoverStyles : {},
      }}
    >
      <Typography
        style={{
          fontSize: fontSize,
          fontWeight: "bold",
          textAlign: "center",
        }}
      >
        {selected && (
          <Box position="absolute">
            <Icon color="primary" style={{ fontSize: fontSize + 3 }}>
              check
            </Icon>
          </Box>
        )}
        {from} - {to}
      </Typography>
    </div>
  );
};

export default HourlySlot;
