import { Box, Grid } from "@material-ui/core";
import { Router } from "@reach/router";
import React, { ReactElement } from "react";
import IPage from "estreeh-common-library/src/interfaces/IPage";
import SideFilterContainer from "../side-filter/SideFilterContainer";
import AgenciesPage from "../agency/AgenciesPage";
import AgencyPage from "../agency/AgencyPage";
import WorkerDrawerPage from "../pages/WorkerDrawerPage";
import SideFilterHidden from "../side-filter/SideFilterHidden";
import NiceBox from "../common/NiceBox";
import { useStoreState } from "../../helpers/StoreTypedHooks";



export default function AgenciesDataProvider(props: IPage): ReactElement {
  const dataLoaded = useStoreState((state) => state.dataLoaded.item);
  const agencies = useStoreState((state) => state.agency.items);
  const agenciesContract = useStoreState(
    (state) => state.agencyTwoYear.items
  );
  const workersTwoYear = useStoreState((state) => state.workerTwoYear.items);
  const showSortButton = false;

  const isMd = props.isMd;
  const toRender =
    agencies.length > 0 && agenciesContract.length > 0 ? (
      <Router primary={false}>
        <AgenciesPage
          agencies={agenciesContract}
          path="contract"
          showContractInfo={true}
        />

        {/* FUTURE: Enable lease pages when store is complete */}

        {/* <AgenciesPage
        agencies={props.toolboxData.agenciesLeasedHourly}
        path="leased-hourly"
        showRentedInfo={true}
        toolboxData={props.toolboxData}
      />
      <AgenciesPage
        agencies={props.toolboxData.agenciesLeasedMonthly}
        path="leased-monthly"
        showRentedInfo={true}
        toolboxData={props.toolboxData}
      /> 
            <AgencyPage
        agencies={props.toolboxData.agenciesLeasedHourly}
        showRentalTabs={true}
        path="leased-hourly/:agencyId"
        toolboxData={props.toolboxData}
        setToolbox={props.setToolbox}
      />
      <AgencyPage
        agencies={props.toolboxData.agenciesLeasedMonthly}
        showRentalTabs={true}
        path="leased-monthly/:agencyId"
        toolboxData={props.toolboxData}
        setToolbox={props.setToolbox}
      />
              <AgencyPage
          agencies={agencies}
          showRentalTabs={true}
          path="leased/:agencyId"
          toolboxData={props.toolboxData}
          setToolbox={props.setToolbox}
        />
      */}
        <AgencyPage
          agencies={agenciesContract}
          showTwoYearTabs={true}
          path="contract/:agencySlug"
        />
        <WorkerDrawerPage
          workersTwoYear={workersTwoYear}
          path="contract/:agencyId/worker-details/:workerTwoYearId"
        />
      </Router>
    ) : (
      <NiceBox>{dataLoaded ? <>There are no workers that match your current filters.</> : <>Loading...</>}</NiceBox>
    );

  return (
    <Grid container spacing={0}>
      <Grid item xs={12} md={3}>
        <Box pr={isMd ? 2 : 0}>
          <Router primary={false}>
            <SideFilterHidden path="contract/:agencyId/worker-details/:workerTwoYearId" />
            <SideFilterContainer
              default
              showSortButton={showSortButton}
              is2Year={true}
              isLeased={false}
              isMd={isMd}
            />
            <SideFilterContainer
              path="leased-monthly/*"
              showSortButton={showSortButton}
              is2Year={false}
              isLeased={true}
              isMd={isMd}
            />
            <SideFilterContainer
              path="leased-hourly/*"
              showSortButton={showSortButton}
              is2Year={false}
              isLeased={true}
              isMd={isMd}
            />
          </Router>
        </Box>
      </Grid>
      <Grid item xs={12} md={9}>
        {toRender}
      </Grid>
    </Grid>
  );
}
