import { navigate, RouteComponentProps } from "@reach/router";
import React, { ReactElement, useState } from "react";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";
import { LoginForm } from "./LoginForm";
import { RegisterStart } from "./RegisterStart";

interface Params {
  demoLink: string;
}

interface Props extends RouteComponentProps<Params> {
  intent: "login" | "signUp";
}

export default function LoginSignUpPage(props: Props): ReactElement {

  const syncFav = useStoreActions(a => a.syncFav);
  const [intent, setIntent] = useState(props.intent);

  
  const returnUrlStored = useStoreState((s) => s.returnUrl.item);
  const setReturnUrl = useStoreActions((a) => a.returnUrl.setItem);

  const navigateTo = returnUrlStored === undefined ? -1 : returnUrlStored;

  function onLogin(){
    const url = navigateTo;
    setReturnUrl(undefined);
    syncFav();
    if (url === -1) navigate(url);
    else navigate(url);
  }

  return <>
    {intent === "signUp" && (<RegisterStart {...props} setIntent={setIntent} onLogin={onLogin} />)}
    {intent === "login" && (<LoginForm {...props} setIntent={setIntent} onLogin={onLogin} />)}
  </>;
}
