import { Box, Grid } from "@material-ui/core";
import React, { ReactElement } from "react";
import Branding from "../../../config/Branding";

interface Props {
  children: React.ReactNode;
  title?: string;
  image?: string;
}

export default function FpStep(props: Props): ReactElement {
  const height = 180;
  const title = props.title ? props.title : "Step #";
  return (
    <Grid item xs={12} sm={4}>
      <Box
        borderRadius="100%"
        bgcolor="#ffffff"
        // height={height}
        boxShadow={Branding.shadow.niceShadow}
        width={height}
        mx="auto"
      >
        <Grid
          container
          alignItems="center"
          alignContent="center"
          style={{ height: height }}
        >
          <Grid item>
            <Box textAlign="center" width={height}>
              {props.image && (
                <Box m={2}>
                  <img
                    alt={props.title}
                    src={props.image}
                    style={{
                      display: "block",
                      maxHeight: 60,
                      marginLeft: "auto",
                      marginRight: "auto",
                    }}
                  />
                </Box>
              )}
              <Box m={1}>
                <Box fontWeight="bold" mb={0.5}>
                  {title}
                </Box>
                <Box pb={2}>{props.children}</Box>
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Box>
    </Grid>
  );
}
