import { Box, Grid, Typography } from "@material-ui/core";
import React, { CSSProperties } from "react";
import Branding from "../../config/Branding";
import Landing1 from "../../images/land.jpg";
import Landing1Mobile from "../../images/landmobile.jpg";
import TopMenu from "./TopMenu";
import { useTranslation } from "react-i18next";
import { ISiteHeadProps } from "estreeh-common-library/src/interfaces/ISiteHeadProps";
import Logo from "../common/Logo";
import TextLoop from "react-text-loop";

const SiteHeadLarge = (props: ISiteHeadProps) => {
  const { t } = useTranslation();

  const sloganStyle: CSSProperties = {
    color: "black",
    fontWeight: "bold",
    textShadow: "1px 1px 6px #ffffff,1px 1px 9px #ffffff",
  };

  const isMd = props.isMd !== undefined ? props.isMd : true;
  const burgerWidth = props.burgerWidth ? props.burgerWidth : 1;

  const sloganBig = (
    <Typography variant={isMd ? "h3" : "h6"} style={sloganStyle}>
      {t("find-the-intro")}
      <TextLoop>
        <span>{t("intro-loop.one")}</span>
        <span>{t("intro-loop.two")}</span>
        <span>{t("intro-loop.three")}</span>
        <span>{t("intro-loop.four")}</span>
      </TextLoop>
      {!props.isMd && (
        <span>
          <br />
          {t("start-choosing-option-short")}:
        </span>
      )}
    </Typography>
  );

  const sloganSmall = (
    <Grid item xs={7}>
      <Box my={1}>
        <Typography variant="h4" style={sloganStyle}>
          {t("start-choosing-option")}:
        </Typography>
      </Box>
    </Grid>
  );

  const extraBox = false;

  return (
    <React.Fragment>
      {!isMd && (
        <TopMenu isBurger={true} burgerWidth={burgerWidth} burgerLeft={24}/>
      )}

      <Box pb={isMd ? 0 : 4}>
        <Box
          bgcolor={Branding.lightest}
          style={{
            backgroundImage: `url(${isMd ? Landing1 : Landing1Mobile})`,
            backgroundSize: "cover",
            backgroundPositionX: "100%",
            backgroundPositionY: "20%",
          }}
        >
          <Box px={3} pb={isMd ? 12 : 4}>
            <Box>
              <Box pt={2} pb={isMd ? 3 : 18}>
                <Grid container>
                  <Grid item xs={isMd ? 2 : 12}>
                    <Logo
                      marginLeft={16 + burgerWidth}
                      maxHeight={isMd ? 80 : 40}
                      marginRight={16}
                    />
                  </Grid>
                  {isMd && (
                    <Grid item xs={6}>
                      <Box bgcolor="#ffffff77" boxShadow="0 0 20px #fff">
                        <TopMenu isBurger={false}/>
                      </Box>
                    </Grid>
                  )}
                </Grid>
              </Box>
              <Grid container spacing={0}>
                {!extraBox && (
                  <Grid item xs={12}>
                    <Box my={isMd ? 3 : 1}>{sloganBig}</Box>
                  </Grid>
                )}
                {props.isMd && sloganSmall}
              </Grid>
            </Box>
          </Box>
        </Box>
        {extraBox && (
          <Box px={3} py={2} bgcolor="#FDF6ED">
            {sloganBig}
          </Box>
        )}
      </Box>
    </React.Fragment>
  );
};

export default SiteHeadLarge;
