import { Box, Typography } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import NiceBox from "../common/NiceBox";
import ChangePasswordForm from "./ChangePasswordForm";
import * as Yup from "yup";
import { Formik } from "formik";
import axios from "axios";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { toast } from "react-toastify";
import { IApiReqUpdateMyProfile } from "estreeh-common-library/src/interfaces/api/req/IApiReqUpdateMyProfile";

interface Props extends RouteComponentProps {}

export default function ChangePasswordPage(props: Props): ReactElement {
  const initialAppForm: IApiReqUpdateMyProfile = {};

  return (
    <Box>
      <Box py={2}>
        <Typography variant="h4">Change Password</Typography>
      </Box>
      <NiceBox>
        <Formik
          validationSchema={Yup.object({
            password: Yup.string()
              .required("Required")
              .min(5, "Minimum 5 characters"),
            confirmPassword: Yup.string()
              .required("Required")
              .oneOf([Yup.ref("password"), null], "Passwords must match"),
          })}
          initialValues={initialAppForm}
          onSubmit={(values, { setSubmitting }) => {
            setSubmitting(true);
            axios
              .put<IResponse<boolean>>(
                process.env.REACT_APP_API_ROOT + "/auth/me",
                values,
                { withCredentials: true }
              )
              .then((result) => {
                toast.success(result.data.message);
              })
              .catch((reason) => {
                toast.error(reason.response.data.message);
              })
              .finally(() => {
                setSubmitting(false);
              });
          }}
        >
          {(formik) => <ChangePasswordForm formik={formik} />}
        </Formik>
      </NiceBox>
    </Box>
  );
}
