import { Formik } from "formik";
import React, { ReactElement } from "react";
import { IApiReqRegisterUser } from "estreeh-common-library/src/interfaces/api/req/IApiReqRegisterUser";
import LoginHeader from "./LoginHeader";
import RegisterCustomerForm from "./RegisterCustomerForm";
import * as Yup from "yup";
import axios from "axios";
import { IApiResRegisterUser } from "estreeh-common-library/src/interfaces/api/res/IApiResRegisterUser";
import { toast } from "react-toastify";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { useStoreActions } from "../../helpers/StoreTypedHooks";

interface Props {
  mobile: string;
  secret: string;
  onLogin: Function;
}

export default function RegisterCustomer({
  mobile,
  secret,
  onLogin,
}: Props): ReactElement {
  const loadUser = useStoreActions((s) => s.loadUser);

  const initialAppForm: IApiReqRegisterUser = {
    secret,
    mobile: `+973${mobile}`,
    fullName: "",
    password: "",
  };

  return (
    <>
      <LoginHeader title="Complete Sign-up" />
      <Formik
        validationSchema={Yup.object({
          fullName: Yup.string()
            .required("Required")
            .min(3, "Minimum 3 characters"),
          password: Yup.string()
            .required("Required")
            .min(5, "Minimum 5 characters"),
          confirmPassword: Yup.string().required("Required").oneOf(
            [Yup.ref("password"), null],
            "Passwords must match"
          ),
        })}
        initialValues={initialAppForm}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          axios
            .post<IResponse<IApiResRegisterUser>>(
              process.env.REACT_APP_API_ROOT + "/auth/register",
              values,
              { withCredentials: true }
            )
            .then((result) => {
              toast.success(result.data.message);
              loadUser().then((user) => {
                onLogin();
              });
            })
            .catch((reason) => {
              toast.error(reason.response.data.message);
            })
            .finally(() => {
              setSubmitting(false);
            });
        }}
      >
        {(formik) => <RegisterCustomerForm formik={formik} />}
      </Formik>
    </>
  );
}
