import { Box, Icon } from "@material-ui/core";
import { Link } from "@reach/router";
import React from "react";
import UseStyles from "../../config/UseStyles";
import { RibbonContainer, RightCornerRibbon } from "react-ribbons";
import Branding from "../../config/Branding";

const WizardCard = (props: {
  to: string;
  children: any;
  icon?: any;
  title?: string;
  isMd: boolean;
  disabled: boolean;
}) => {
  const classes = UseStyles();

  return (
    <RibbonContainer>
      {props.disabled === true && (
        <RightCornerRibbon
          backgroundColor={Branding.primary}
          // backgroundColor="#0088ff"
          color="#f0f0f0"
        >
          <span style={{ fontSize: "80%" }}>Coming Soon!</span>
        </RightCornerRibbon>
      )}
      <Link
        to={props.disabled ? "" : props.to}
        className={
          props.disabled
            ? classes.wizardCardLinkDisabled
            : classes.wizardCardLink
        }
        onClick={
          !props.disabled
            ? undefined
            : () => {
                // TODO: Make this message nicer?
                alert(`"${props.title}" services are Coming soon!`);
              }
        }
      >
        <Box py={4} px={2} color={props.disabled ? "#bbb" : undefined}>
          <Box>
            {/* <span className="MuiIcon-root material-icons-two-tone MuiIcon-colorPrimary" style={{ fontSize: 50 }}>{props.icon}</span> */}
            <Icon
              // className="material-icons-two-tone"
              // color="primary"
              style={{ fontSize: 50 }}
            >
              {props.icon}
            </Icon>
          </Box>
          <Box fontSize={props.isMd ? "h6.fontSize" : 15}>{props.children}</Box>
        </Box>
      </Link>
    </RibbonContainer>
  );
};

export default WizardCard;
