import { Box, Typography } from "@material-ui/core";
import React from "react";
import Branding from "../../config/Branding";
import IPaymentSummaryItem from "estreeh-common-library/src/interfaces/IPaymentSummaryItem";
import IWorkerRental from "estreeh-common-library/src/interfaces/IWorkerRental";
import AgencyTabRemarks from "../agency/AgencyTabRemarks";
import PaymentSummaryItem from "../common/PaymentSummaryItem";

const RequestPaymentSummary = (props: {
  paymentItems: IPaymentSummaryItem[];
  remarks?:string;
  workerRental?: IWorkerRental;
}) => {
  const headerVariant = "h6";

  let total: number = 0;
  props.paymentItems.forEach((item) => {
    total += item.amount;
  });

  //TODO: Add the new items from Backend (Process Fees, fixed fees)

  return (
    <>
      <Box pb={1} pt={2}>
        <Typography variant={headerVariant}>Payment Summary</Typography>
      </Box>
      <Box maxWidth={340}>
        {props.paymentItems.map((item) => (
          <PaymentSummaryItem title={item.title} amount={item.amount} />
        ))}
        {/* <PaymentSummaryItem title="VAT (5%)" amount={0} /> */}
        <PaymentSummaryItem
          title="Total Amount"
          amount={total}
          emphasized={true}
        />
      </Box>
      {props.workerRental &&
        props.workerRental.service &&
        props.workerRental.service.remarks && (
          <Box my={1}>
            <AgencyTabRemarks text={props.workerRental.service?.remarks} />
          </Box>
        )}
      <Box
        my={2}
        fontStyle="italic"
        color={Branding.text.warning}
        fontWeight="bold"
      >
        {props.remarks}
      </Box>
    </>
  );
};

export default RequestPaymentSummary;
