import Faker from "faker";
import { demoItemsCount } from "../config/DemoItems";


export function getDemoId(nationality: string): number {
  let demoItem = demoItemsCount.find((x) => x.name === nationality);
  if (demoItem) {
    return Faker.random.number({ min: 1, max: demoItem.count });
  }
  return 0;
}
