import { Box, Grid, GridSize, Typography } from "@material-ui/core";
import React from "react";
import IHourlySlot from "estreeh-common-library/src/interfaces/IHourlySlot";
import HourlySlot from "./HourlySlot";
import dateFormat from "dateformat";
import Branding from "../../../config/Branding";

const HourlySlotsDay = (props: { dateKey: string; slots: IHourlySlot[]; md:GridSize }) => {
  const date: Date | undefined =
    props.slots.length > 0 ? props.slots[0].date : new Date(props.dateKey);

  const items = props.slots.map((slot) => <HourlySlot slot={slot} />);

  const day = dateFormat(date, "ddd");
  const dayMonth = dateFormat(date, "d-mmm");

  const textStyle: React.CSSProperties = {
    fontSize: Branding.fontSize.small,
    textAlign: "center",
    fontWeight: "bold",
    // color: Branding.text.secondary,
  };

  return (
    <Grid item md={props.md} xs={6}>
      <Box px={0.5} py={1}>
        <Grid container>
          <Grid item xs={12}>
            <Typography style={textStyle}>
              {day}&nbsp;{dayMonth}
            </Typography>
          </Grid>
        </Grid>
        <Box>{items}</Box>
      </Box>
    </Grid>
  );
};

export default HourlySlotsDay;
