import { Box, Container } from "@material-ui/core";
import Branding from "../../config/Branding";
import { Router } from "@reach/router";
import SiteHead from "./SiteHead";
import AgenciesDataProvider from "../data/AgenciesDataProvider";
import RequestWorkerPage from "../request/RequestWorkerPage";
import SuccessPage from "../pages/SuccessPage";
import RequestConfirmationPage from "../request/RequestConfirmationPage";
import FilterPage from "../pages/FilterPage";
import SiteHeadless from "./SiteHeadless";
import { withBreakpoints } from "react-breakpoints";
import MyRequestsPage from "../user/MyRequestsPage";
import SelectSchedulePage from "../pages/SelectSchedulePage";
import CookieConsent from "react-cookie-consent";
import Footer2 from "../common/Footer2";
import LoginSignUpPage from "../login/LoginSignUpPage";
import MyLayout from "./MyLayout";
import ContactUsPage from "../pages/ContactUsPage";
import MyFavoritesPage from "../user/MyFavoritesPage";
import ChangePasswordPage from "../user/ChangePasswordPage";
import MyProfilePage from "../user/MyProfilePage";
import ForgotPasswordPage from "../password-reset/ForgotPasswordPage";
import Footer1 from "../common/Footer1";
import RequestDetailsPage from "../user/RequestDetailsPage";

const DefaultLayout = (props: {
  classes: any;
  breakpoints: any;
  currentBreakpoint: any;
}) => {
  const { breakpoints, currentBreakpoint } = props;
  const isMd = breakpoints[currentBreakpoint] >= breakpoints.md;
  const burgerWidth = 26;

  /* TODO: New pages:
  =======================

  Sign Up,
  User profile, 
  reset password, 
  forgot password
  favorites:
    - Favorites list
    - Add worker to favorites (function)
    - Remove worker from favorite (function)
    - Same buttons as worker2year card (Fav, Select, Details)
  */

  return (
    <Box bgcolor={Branding.lightBg}>
      <CookieConsent>
        This website uses cookies to enhance the user experience.
      </CookieConsent>
      <Router primary={false}>
        <SiteHead default isMd={isMd} burgerWidth={burgerWidth} />
        <SiteHeadless
          path="agencies/contract-filters"
          showCancelText={true}
          title="Filters"
          rightText="Clear All"
        />
        <SiteHeadless
          path="agencies/leased-filters"
          showCancelText={true}
          title="Filters"
        />
      </Router>
      <Box id="page-content-container" style={{ height: "100%" }}>
        <Container>
          <Router primary={false}>
            <AgenciesDataProvider
              isMd={isMd}
              path="agencies/*"

              // translationPack={{ t, i18n }}
            />
            <FilterPage path="agencies/contract-filters" is2Year={true} />
            <FilterPage path="agencies/leased-filters" isLeased={true} />
            <LoginSignUpPage
              path="/login"
              demoLink="/new-request/two-year-worker"
              intent="login"
            />
            <LoginSignUpPage path="/login-prompt" intent="signUp" />
            {/* <LoginForm
              path="/login/lease"
              demoLink="/new-request/two-year-worker"
            />
            <LoginForm
              path="/login/month"
              demoLink="/new-request/two-year-worker"
            /> */}
            <SelectSchedulePage
              path="/new-request/select-hours"
              link="/new-request/hourly-worker"
              isHourly={true}
            />
            <SelectSchedulePage
              path="/new-request/select-months"
              link="/new-request/monthly-worker"
              isHourly={false}
            />
            <RequestWorkerPage
              path="/new-request/two-year-worker/:guid"
              isTwoYear={true}
              link="/new-request/confirm"
              isMd={isMd}
            />
            <RequestWorkerPage
              path="/new-request/hourly-worker/:guid"
              isRentalHourly={true}
              link="/new-request/confirm-lease"
              isMd={isMd}
            />
            <RequestWorkerPage
              path="/new-request/monthly-worker/:guid"
              isRentalMonthly={true}
              link="/new-request/confirm-lease"
              isMd={isMd}
            />
            <RequestConfirmationPage
              path="/new-request/confirm"
              link="/success"
            />
            <RequestConfirmationPage
              path="/new-request/confirm-lease"
              link="/success-lease"
            />
            <SuccessPage
              path="/success"
              message="Your request has been sent."
              smallMessage="The agency will contact you within 1 business day."
            />
            <SuccessPage
              path="/success-lease"
              message="Your booking is completed."
              smallMessage="The agency may contact you if further details are required."
            />
            <MyLayout path="my">
              <MyFavoritesPage path="favorites"/>
              <MyRequestsPage path="requests" default />
              <RequestDetailsPage path="requests/:id" purpose="view" />
              <RequestDetailsPage path="requests/:id/cancel" purpose="cancel" />
              <ChangePasswordPage path="password" />
              <MyProfilePage path="profile" />
            </MyLayout>
            <ForgotPasswordPage path="forgot-password" />
            <ContactUsPage path="contact-us"/>
          </Router>
        </Container>
        <Footer1 />
        <Footer2 />
      </Box>
    </Box>
  );
};

export default withBreakpoints(DefaultLayout);
