import { Avatar, Box, Grid, Typography } from "@material-ui/core";
import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import React, { ReactElement } from "react";
import AgencyContactInfo from "./AgencyContactInfo";
import AgencyRatingInfo from "./AgencyRatingInfo";

interface Props {
  agency: IAgency;
  showContact: boolean;
}

export default function AgencyInfoCard({
  agency,
  showContact,
}: Props): ReactElement {
  return (
    <Grid container spacing={2}>
      <Grid item md={3} xs={12}>
        <Avatar
          variant="rounded"
          src={process.env.REACT_APP_IMAGES_ROOT + "" + agency.logo}
          style={{
            flexShrink: 0,
            width: "100%",
            minHeight: 100,
          }}
        />
      </Grid>
      <Grid item md={9} xs={12}>
        <Typography variant="h5">{agency.title}</Typography>
        <Box>
          <AgencyRatingInfo agency={agency} />
          {showContact && <AgencyContactInfo agency={agency} />}
        </Box>
      </Grid>
    </Grid>
  );
}
