import IAgency from "estreeh-common-library/src/interfaces/IAgency";

export const removeAgencyWithoutTwoYearWorkers = (
  arr: IAgency[]
): IAgency[] => {
  let i = 0;
  while (i < arr.length) {
    const index = arr.findIndex(
      (agency) =>
        agency.workersTwoYear === undefined || agency.workersTwoYear.length < 1
    );
    if (index > -1) {
      arr.splice(index, 1);
    } else {
      i++;
    }
  }

  return arr;
};
