import { Checkbox, FormControlLabel } from "@material-ui/core";
import React, { ReactElement } from "react";
import SmallText from "../common/SmallText";

interface Props {
  title: string;
  value?: boolean;
  onChange?:
    (event: React.ChangeEvent<HTMLInputElement>, checked: boolean) => void;
}

export default function FilterCheckboxItem(props: Props): ReactElement {
  return (
    <FormControlLabel
      control={
        <Checkbox
          color="primary"
          size="small"
          style={{ padding: 4, marginLeft: 16 }}
          checked={props.value}
          onChange={props.onChange}
        />
      }
      label={<SmallText style={{ userSelect:"none" }}>{props.title}</SmallText>}
    />
  );
}
