import { Grid, GridSize } from "@material-ui/core";
import React from "react";
import IWorkerRental from "estreeh-common-library/src/interfaces/IWorkerRental";
import MonthlySlot from "./MonthlySlot";
import WorkerRentalCardHourlySlots from "./WorkerRentalCardHourlySlots";

const WorkerRentalCardSlots = (props: {
  worker: IWorkerRental;
  isHourly: boolean;
  isMonthly: boolean;
  md: GridSize;
  maxDays: number;
  maxMonths: number;
}) => {
  let hourlySlots = props.worker.hourlySlotsDictionary;
  let monthlySlots = props.worker.monthlySlots;

  if (monthlySlots.length > props.maxMonths) {
    monthlySlots.length = props.maxMonths;
  }
  return (
    <React.Fragment>
      {props.isHourly && (
        <WorkerRentalCardHourlySlots slotsDictionary={hourlySlots} md={props.md} maxDays={props.maxDays} />
      )}
      {props.isMonthly && (
        <Grid container>
          {monthlySlots.map((slot) => (
            <MonthlySlot slot={slot} key={slot.id} md={props.md} />
          ))}
        </Grid>
      )}
    </React.Fragment>
  );
};

export default WorkerRentalCardSlots;
