import React from "react";
import { useTranslation } from "react-i18next";
import {
  maxProcessingFees,
  minProcessingFees,
} from "../../config/FilterOptions";
import { IFilterGroupContainer } from "estreeh-common-library/src/interfaces/IFilterGroupContainer";
import FilterGroup from "./FilterGroup";
import FilterSlider from "./FilterSlider";

const FGProcessFees = (props: IFilterGroupContainer) => {
  const {t} = useTranslation();
  const expanded = props.isMd
    ? props.expandedDefaultDesktop
    : props.expandedDefaultMobile;
  return (
    <FilterGroup title={t('process-fees-bd')} expanded={expanded}>
      <FilterSlider
        defaultValue={[minProcessingFees, maxProcessingFees]}
        min={minProcessingFees}
        max={maxProcessingFees}
        step={50}
        isMd={props.isMd}
      />
    </FilterGroup>
  );
};

export default FGProcessFees;
