import { Grid } from "@material-ui/core";
import React from "react";
import IAgencyTab from "estreeh-common-library/src/interfaces/IAgencyTab";
import WorkerRentalGridCard from "../worker/rental/WorkerRentalGridCard";
import Worker2CardWrapper from "../worker/two-years/Worker2CardWrapper";

const AgencyTabPanelGridDesign = (props: {
  tab: IAgencyTab;
  isHourly: boolean;
  isMonthly: boolean;
}) => {

  // Hides the hidden workers
  const workersTwoYear = props.tab.workersTwoYear?.map(x => x).filter(x => x.hidden === false && x.collected === false);

  return (
    <Grid container spacing={1}>
      {workersTwoYear && workersTwoYear.map((worker) => (
        <Worker2CardWrapper
          worker={worker}
          design="grid"
          key={worker._id}
        />
      ))}
      {props.tab.workersRental?.map((worker) => (
        <WorkerRentalGridCard
          worker={worker}
          isHourly={props.isHourly}
          isMonthly={props.isMonthly}
          showButtons={true}
          key={worker._id}
        />
      ))}
    </Grid>
  );
};

export default AgencyTabPanelGridDesign;
