import { Grid } from "@material-ui/core";
import React from "react";
import SmallText from "./SmallText";

const ConfirmationSummaryItem = (props: {
  title: string;
  value: string | undefined;
}) => {
  return (
    <Grid container spacing={1}>
      <Grid item md={4} xs={6}>
        <SmallText style={{ fontWeight: "bold" }}>{props.title}</SmallText>
      </Grid>
      <Grid item md={8} xs={6}>
        <SmallText>{props.value}</SmallText>
      </Grid>
    </Grid>
  );
};

export default ConfirmationSummaryItem;
