import { Box, Container } from "@material-ui/core";
import Color from "color";
import React, { ReactElement } from "react";

interface Props {
  children: React.ReactNode;
  bgColor?: string;
  py?:number;
}

export default function FpSection(props: Props): ReactElement {
  const bgColor = props.bgColor !== undefined ? props.bgColor : "#FFFFFF";
  const color = Color(bgColor);
  const textColor = color.isDark() ? "#FFFFFF" : "#000000";
  const py = props.py !== undefined ? props.py : 6;
  // const maxWidth = 1440;

  return (
    <Box bgcolor={bgColor}>
      <Container>
        <Box py={py} color={textColor}>
          {props.children}
        </Box>
      </Container>
    </Box>
  );
}
