import React, { ReactElement } from "react";
import { ICountryChip } from "estreeh-common-library/src/interfaces/ICountryChip";

interface Props {
  country: ICountryChip;
  showFees: boolean;
  height?: number;
  marginRight?: number;
}

export default function CountryFlag(props: Props): ReactElement {
  const flagStyle: React.CSSProperties = {
    height: props.height !== undefined ? props.height : 12,
    // verticalAlign: "text-bottom",
    // verticalAlign: "middle",
    marginRight: props.marginRight !== undefined ? props.marginRight : 4,
    // border:"1px solid #888",
    boxShadow:"0 0 3px #666",
    display:"inline",
  };

  return (
    <>
      <img
        alt={props.country.country}
        src={
          "http://purecatamphetamine.github.io/country-flag-icons/3x2/" +
          props.country.countryCode +
          ".svg"
        }
        style={flagStyle}
      />
      {props.showFees ? props.country.titleTranslatedWithFees : props.country.titleTranslated}
    </>
  );
}
