import { Avatar } from "@material-ui/core";
import React, { ReactElement } from "react";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import { S3Folder } from "estreeh-common-library/src/types/S3Folder";

interface Props {
  worker: IWorkerTwoYear;
  variant?: 1 | 2;
  width?: number | string;
  height?: number | string;
}

export default function Worker2SmallPic({
  worker,
  height,
  width,
  variant,
}: Props): ReactElement {
  let paddingLeft: string | undefined;
  let paddingRight: string | undefined;
  if (variant === 1) {
    width = "100%";
    height = 151;
  }
  if (variant === 2) {
    width = "92%";
    height = 90;
    paddingLeft = "4%";
    paddingRight = "4%";
  }
  const folder: S3Folder = "2y/sm";
  return (
    <Avatar
      src={`${process.env.REACT_APP_IMAGES_ROOT}/${folder}/${worker.id}.${worker.smallPictureFileExt}`}
      style={{
        width,
        minHeight: height,
        alignSelf: "center",
        paddingLeft,
        paddingRight,
      }}
      variant="rounded"
    />
  );
}
