import React from "react";
import { ThemeProvider } from "@material-ui/core/styles";
import ReactBreakpoints from "react-breakpoints";
import "fontsource-roboto";
import UseStyles from "../../config/UseStyles";
import MyTheme from "../../config/MyTheme";
import { myBreakpoints } from "../../config/Breakpoints";
import ToolboxState from "../data/ToolboxState";
import { useTranslation } from "react-i18next";
import Branding from "../../config/Branding";



export default function MasterTheme(props:{
  useRtl:boolean;
}) {
  const classes = UseStyles();
  const {i18n} = useTranslation();
  const lang = i18n.language;
  const dir = props.useRtl && lang === "ar" ? "rtl" : "ltr";


  document.documentElement.dir = dir;
  document.documentElement.lang = lang;
  document.body.dir = dir;
  document.body.lang = lang;

  // console.log(MyTheme);

  return (
    <ReactBreakpoints breakpoints={myBreakpoints}>
      <ThemeProvider theme={MyTheme}>
        <div className={classes.root} dir={dir} style={{ height:"100%",fontFamily:Branding.fontFamily.body }}>
          <ToolboxState classes={classes}/>
        </div>
      </ThemeProvider>
    </ReactBreakpoints>
  );
}
