import AgencyCard from "./AgencyCard";
import IAgenciesPage from "estreeh-common-library/src/interfaces/IAgenciesPage";
import ScrollToTop from "../common/ScrollToTop";
import FilteredResultsAlert from "../common/FilteredResultsAlert";
import InfiniteScroll from "react-infinite-scroll-component";
import React, { ReactElement, useEffect, useState } from "react";
import IAgency from "estreeh-common-library/src/interfaces/IAgency";

export default function AgenciesPage({
  agencies,
  showContractInfo,
  showRentedInfo,
}: IAgenciesPage): ReactElement {
  // TODO: If selected by month, message / button for "With stay over? I want stay over?" which links to safety information
  // TODO: Search bar

  const [maxDisplay, setMaxDisplay] = useState(100);
  const [agenciesTrimmed, setAgenciesTrimmed] = useState<IAgency[]>([]);
  let agencyComponents: React.ReactNode = <>No agencies</>;

  useEffect(() => {
    if (agencies !== undefined) {
      setAgenciesTrimmed(agencies.slice(0, maxDisplay));
    }
  }, [agencies, maxDisplay]);

  if (agenciesTrimmed !== undefined) {
    agencyComponents = agenciesTrimmed.map((agency) => (
      <AgencyCard
        key={agency._id}
        agency={agency}
        showContractInfo={showContractInfo}
        showTabChips={showRentedInfo}
        showRating={false}
      />
    ));
  }

  return (
    <>
      <ScrollToTop />
      <FilteredResultsAlert />
      {agenciesTrimmed !== undefined && agencies !== undefined && (
        <InfiniteScroll
          dataLength={agenciesTrimmed.length} //This is important field to render the next data
          next={() => {
            setMaxDisplay(maxDisplay + 5);
          }}
          hasMore={agenciesTrimmed.length < agencies.length}
          loader={<h4>Loading...</h4>}
          // endMessage={
          //   <p style={{ textAlign: "center" }}>
          //     <b>Yay! You have seen it all</b>
          //   </p>
          // }
        >
          {agencyComponents}
        </InfiniteScroll>
      )}
    </>
  );
}
