import { Box, Icon } from "@material-ui/core";
import React from "react";
import SmallText from "./SmallText";

const SmallInfo = (props: { icon: string; children: React.ReactNode }) => {
  const iconFontSize = "inherit";

  return (
    <Box>
      <SmallText>
        <Icon fontSize={iconFontSize}>{props.icon}</Icon>&nbsp;
        {props.children}
      </SmallText>
    </Box>
  );
};

export default SmallInfo;
