import { TextField } from "@material-ui/core";
import { CSSProperties } from "@material-ui/styles";
import React, { ReactElement } from "react";
import { changeIfNumber } from "../../helpers/ChangeIfNumber";
import MobileAdornment from "./MobileAdornment";

interface Props {
  onChange?: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement>;
  onKey?: React.KeyboardEventHandler<HTMLDivElement>;
  value: any;
  isErrored: boolean;
  disabled: boolean;
}

export default function MobileInput({
  onChange,
  onKey,
  value,
  isErrored,
  disabled,
}: Props): ReactElement {
  const textBoxStyle: CSSProperties = {
    backgroundColor: isErrored ? "#ffeeee" : "#ffffff",
    // WebkitAppearance: "none",
    // MozAppearance: "textfield",
  };

  return (
    <TextField
      name="mobile"
      label="Mobile"
      variant="outlined"
      value={value}
      onChange={(event) =>
        changeIfNumber({
          event,
          onChange,
        })
      }
      style={textBoxStyle}
      onKeyPress={onKey}
      disabled={disabled}
      // error={isErrored}
      InputProps={{
        startAdornment: <MobileAdornment />,
      }}
      inputProps={{ maxLength: 8 }}
      type="tel"
    />
  );
}
