import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import IAgencyTab from "estreeh-common-library/src/interfaces/IAgencyTab";


export function MakeTabsServices(agency: IAgency, index: number): number {
  let title = "Packages";

  let tab: IAgencyTab = {
    index: index,
    title: title,
    isRented: false,
    isRentedHourly: false,
    isRentedMonthly: false,
    isServices: true,
    isTwoYearContract: false,
    feesText: "",
    titleWithFees: title,
    services: agency.services,
  };
  agency.tabs.push(tab);
  agency.tabsServices.push(tab);
  index++;

  return index;
}
