import { Box, Button, Divider, Grid, Typography } from "@material-ui/core";
import React, { ReactElement } from "react";
import { useTranslation } from "react-i18next";
import { IRequest } from "estreeh-common-library/src/interfaces/IRequest";
import NiceBox from "../common/NiceBox";
import IconInfo from "../worker/IconInfo";
import Worker2SmallPic from "../worker/two-years/Worker2SmallPic";
import { navigate } from "@reach/router";
import RequestStatusPill from "./RequestStatusPill";

interface Props {
  request: IRequest;
}

export default function RequestGridCard(props: Props): ReactElement {
  const { t } = useTranslation();

  const request = props.request;
  const workerTwoYear = request.workerTwoYear;
  const agency = request.agency;
  const cancelColor = "#d99";

  let serviceText = "";

  if (request.isTwoYearContract) serviceText = t("2-years-contract");
  if (request.isLeaseByMonth) serviceText = t("lease-by-month");
  if (request.isLeaseByHour) serviceText = t("lease-by-hour");

  // TODO: SHow agency name + Agency telephone + Other useful info
  // TODO: Show tracking code

  if (workerTwoYear === undefined)
    return <React.Fragment> ID: {request.workerTwoYearId} | </React.Fragment>;
  return (
    <Grid item md={4} xs={12} lg={3}>
      {request.status !== undefined && (
        <NiceBox p={1} my={0}>
          <Box>

            <RequestStatusPill status={request.status} />
            <Typography style={{ fontWeight: "bold" }}>
              {workerTwoYear?.name}
            </Typography>
            <Box py={1}>
              <Divider />
            </Box>
            <Grid container spacing={0}>
              <Grid item xs={5}>
                <Worker2SmallPic worker={workerTwoYear} variant={2} />
              </Grid>
              <Grid item xs={7}>
                <Box minHeight={140}>
                  <IconInfo icon="tag" title="Tracking Code">
                    {request.trackingCode}
                  </IconInfo>
                  <IconInfo icon="business" title="Agency">
                    {agency?.title}
                  </IconInfo>
                  <IconInfo icon="work" title="Job Title">
                    {workerTwoYear.jobTitle}
                  </IconInfo>
                  <IconInfo icon="flag" title="Nationality">
                    {workerTwoYear.nationality}
                  </IconInfo>
                  <IconInfo icon="border_color" title="Service">
                    {serviceText}
                  </IconInfo>
                  <IconInfo title="Address">
                    {request.applicationForm.address}
                  </IconInfo>
                  {/* <IconInfo title="Timing">
                <strong>{request.applicationForm.selectedHours}&nbsp;</strong>
              </IconInfo> */}
                </Box>
              </Grid>
            </Grid>
            <Box py={1}>
              <Divider />
            </Box>
            <Grid container justify="center" spacing={1}>
              {true && (
                <Grid item>
                  <Button
                    variant="outlined"
                    color="primary"
                    onClick={() => {
                      if (request.id !== undefined)
                        navigate(`requests/${request.id}`);
                    }}
                  >
                    Details
                  </Button>
                </Grid>
              )}
              {request.status.allowCancel && (
                <Grid item>
                  <Button
                    variant="outlined"
                    style={{ color: cancelColor, borderColor: cancelColor }}
                    onClick={() => {
                      if (request.id !== undefined)
                      navigate(`requests/${request.id}/cancel`);
                    }}
                  >
                    Cancel
                  </Button>
                </Grid>
              )}
            </Grid>
          </Box>
        </NiceBox>
      )}
    </Grid>
  );
}
