import React from 'react';
import { useTranslation } from 'react-i18next';
import { educationLevels } from '../../config/FilterOptions';
import { IFilterGroupContainer } from 'estreeh-common-library/src/interfaces/IFilterGroupContainer';
import FilterCheckboxItem from './FilterCheckboxItem';
import FilterGroup from './FilterGroup';

const FGEducation = (props:IFilterGroupContainer) => {
  const {t} = useTranslation();
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title={t("educational-level")} expanded={expanded}>
    {educationLevels.map((title) => (
        <FilterCheckboxItem key={title} title={title} />
      ))}
    </FilterGroup>
  );
};

export default FGEducation;