import React from 'react';
import { useTranslation } from 'react-i18next';
import { languageSkills } from '../../config/FilterOptions';
import { IFilterGroupContainer } from 'estreeh-common-library/src/interfaces/IFilterGroupContainer';
import FilterCheckboxItem from './FilterCheckboxItem';
import FilterGroup from './FilterGroup';

const FGLanguageSkills = (props:IFilterGroupContainer) => {
  const {t} = useTranslation();
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title={t('language-skills')} expanded={expanded}>
    {languageSkills.map((title) => (
      <FilterCheckboxItem key={title} title={title} />
    ))}
  </FilterGroup>
  );
};

export default FGLanguageSkills;