
import { useTranslation } from "react-i18next";
import { IFilterGroupContainer } from "estreeh-common-library/src/interfaces/IFilterGroupContainer";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";
// import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import FilterCheckboxItem from "./FilterCheckboxItem";
import FilterGroup from "./FilterGroup";

interface Props extends IFilterGroupContainer {
  jobTitles: string[];
}

const FGJobTitles = (props: Props) => {
  const { t } = useTranslation();
  const updateFiltersTwoYear = useStoreActions(
    (action) => action.updateFiltersTwoYear
  );
  const expanded = props.isMd
    ? props.expandedDefaultDesktop
    : props.expandedDefaultMobile;

  const jobTitles = useStoreState((s) => s.filterOptions.jobTitle.items);
  const setJobTitles = useStoreActions(
    (a) => a.filterOptions.jobTitle.setItems
  );

  return (
    <FilterGroup title={t("job-title")} expanded={expanded}>
      {jobTitles.map((job) => (
        <FilterCheckboxItem
          key={job.key}
          title={job.key}
          value={job.value}
          onChange={(event, checked) => {
            jobTitles.forEach((j) => {
              if (j.key === job.key) {
                j.value = checked;
              }
            });
            setJobTitles({ payload: jobTitles, setUnfiltered: false });
            let array = jobTitles
              .filter((n) => n.value === true)
              .map((n) => n.key);
            updateFiltersTwoYear({
              key: "jobTitle",
              array: array,
            });
          }}
        />
      ))}
    </FilterGroup>
  );
};

export default FGJobTitles;
