import { Avatar, Box, Divider, Grid, Typography } from "@material-ui/core";
import Rating from "@material-ui/lab/Rating";
import { Link } from "@reach/router";
import dateFormat from "dateformat";
import { useState } from "react";
import Branding from "../../../config/Branding";
import IHourlySlot from "estreeh-common-library/src/interfaces/IHourlySlot";
import IWorkerRental from "estreeh-common-library/src/interfaces/IWorkerRental";
import FavoriteButton from "../../common/FavoriteButton";
import NiceBox from "../../common/NiceBox";
import ReserveWorkerButton from "../../common/ReserveWorkerButton";
import SmallText from "../../common/SmallText";
import IconInfo from "../IconInfo";
import HourlySlotsDayMiniDisplay from "./HourlySlotsDayMiniDisplay";
import MonthlySlotsMiniDisplay from "./MonthlySlotsMiniDisplay";
import { useNavigate } from "@reach/router";
import { useStoreActions } from "../../../helpers/StoreTypedHooks";

const WorkerRentalGridCard = (props: {
  worker: IWorkerRental;
  isHourly: boolean;
  isMonthly: boolean;
  picHeight?: number;
  showButtons?: boolean;
}) => {
  const worker = props.worker;
  const [selectedDate, setSelectedDate] = useState(new Date());
  const setWorker = useStoreActions((action) => action.selectedWorkerRental.setItem);

  const addDays = (days: number): void => {
    let newDate = new Date(selectedDate);
    newDate.setDate(newDate.getDate() + days);
    if (getSlots(newDate)) {
      setSelectedDate(newDate);
    }
  };

  const navigate = useNavigate();

  const getDateKey = (iDate?: Date): string => {
    let date = iDate ? iDate : selectedDate;
    return dateFormat(date, "yyyy-mm-dd");
  };

  const getSlots = (iDate?: Date): IHourlySlot[] => {
    return worker.hourlySlotsDictionary[getDateKey(iDate)];
  };

  const getRelevantDaySlots = (days: number): IHourlySlot[] => {
    let d = new Date(selectedDate);
    d.setDate(d.getDate() + days);
    return getSlots(d);
  };

  const onReserveButton = () => {
    setWorker(props.worker);
    navigate(props.isHourly ? hourlyWorkerLink : monthlyWorkerLink);
  };

  const picWidth = "100%";
  const picHeight = 96;
  // const hourlyWorkerLink = "/new-request/hourly-worker";
  // const monthlyWorkerLink = "/new-request/monthly-worker";
  const hourlyWorkerLink = "/new-request/select-hours";
  const monthlyWorkerLink = "/new-request/select-months";

  return (
    <Grid item xs={12} sm={6} md={4}>
      <NiceBox p={1} my={0}>
        <Box>
          <Grid container justify="space-between">
            <Grid item xs={12}>
              <Typography style={{ fontWeight: "bold" }}>
                {worker.name}
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Box textAlign="left">
                <Rating
                  precision={0.5}
                  value={worker.rating}
                  size="small"
                  readOnly
                  style={{ verticalAlign: "middle" }}
                />
                &nbsp;
                <Link to=".">
                  <SmallText
                    style={{ display: "inline", color: Branding.primary }}
                  >
                    Reviews ({worker.reviews.length})
                  </SmallText>
                </Link>
              </Box>
            </Grid>
          </Grid>
          <Box py={1}>
            <Divider />
          </Box>

          <Box>
            <Grid container>
              <Grid item xs={5}>
                <Box textAlign="center">
                  <Avatar
                    src={worker.smallPicture}
                    style={{
                      width: picWidth,
                      minHeight: picHeight,
                      alignSelf: "center",
                    }}
                    variant="rounded"
                  />
                </Box>
              </Grid>
              <Grid item xs={7}>
                <Box px={1}>
                  <IconInfo icon="work">{worker.jobTitle}</IconInfo>
                  <IconInfo icon="flag">{worker.nationality}</IconInfo>
                  <IconInfo icon="attach_money">{worker.feesText}</IconInfo>
                  {/* {worker.remarks && <IconInfo>{worker.remarks}</IconInfo>} */}
                  {props.isHourly && (
                    <HourlySlotsDayMiniDisplay
                      slots={worker.hourlySlotsDictionary[getDateKey()]}
                      leftOnClick={() => addDays(-1)}
                      rightOnClick={() => addDays(1)}
                      showLeftArrow={getRelevantDaySlots(-1) !== undefined}
                      showRightArrow={getRelevantDaySlots(1) !== undefined}
                      date={selectedDate}
                    />
                  )}
                  {props.isMonthly && (
                    <MonthlySlotsMiniDisplay
                      slots={worker.monthlySlots}
                      date={selectedDate}
                    />
                  )}

                  {/* <IconInfo icon="local_airport">
                    {worker.experienceYears} years experience
                  </IconInfo> */}

                  {/* <IconInfo icon="attach_money">{worker.processFeesBhdFormatted}</IconInfo> */}
                </Box>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Box py={1}>
          <Divider />
        </Box>
        <Box textAlign="center">
          <FavoriteButton />
          <ReserveWorkerButton
            link={props.isHourly ? hourlyWorkerLink : monthlyWorkerLink}
            onClick={onReserveButton}
          />
        </Box>
      </NiceBox>
    </Grid>
  );
};

export default WorkerRentalGridCard;
