import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import _ from "lodash";

export const AgenciesHourlyOnly = (agencies: IAgency[]): IAgency[] => {
  let copy = _.cloneDeep(agencies);
  let a = copy.map((agency) => {
    agency.servicesSelectableWorkers = agency.servicesSelectableWorkers?.filter((service) => {
      return service.isRentalHourly;
    });
    agency.tabsRental = agency.tabsRental?.filter((tab) => {
      return tab.isRentedHourly;
    });
    return agency;
  });

  return RemoveBlankServices(a);
};

export const AgenciesMonthlyOnly = (agencies: IAgency[]): IAgency[] => {
  let copy = _.cloneDeep(agencies);
  let a = copy.map((agency) => {
    agency.servicesSelectableWorkers = agency.servicesSelectableWorkers?.filter(
      (service) => {
        return service.isRentalMonthly;
      }
    );
    agency.tabsRental = agency.tabsRental?.filter((tab) => {
      return tab.isRentedMonthly;
    });

    return agency;
  });

  return RemoveBlankServices(a);
};

const RemoveBlankServices = (agencies: IAgency[]): IAgency[] => {
  let a = agencies.filter((agency) => {
    return agency.tabsRental !== undefined && agency.tabsRental.length > 0;
  });

  return a;
};
