import { Box, Divider, Typography } from "@material-ui/core";
import React from "react";
import OrangeChip from "../common/OrangeChip";

const DetailsGroup = (props: {
  title?: string;
  titleChip?: string;
  children?: React.ReactNode;
}) => {
  const titleChip = props.titleChip ? (
    <OrangeChip label={props.titleChip} />
  ) : null;

  const title = props.title ? (
    <Box pb={1}>
      <Typography variant="h6">
        {props.title} {titleChip}
      </Typography>
    </Box>
  ) : null;

  return (
    <Box>
      <Divider variant="middle" />
      <Box py={1}>
        {title}
        <Box py={1}>{props.children}</Box>
      </Box>
    </Box>
  );
};

export default DetailsGroup;
