import { Box, Grid } from "@material-ui/core";
import dateFormat from "dateformat";
import React, { ReactElement } from "react";
import IMonthlySlot from "estreeh-common-library/src/interfaces/IMonthlySlot";
import SlotMiniDisplay from "./SlotMiniDisplay";

interface Props {
  date: Date;
  slots: IMonthlySlot[];
  numDisplay?: number;
}

export default function MonthlySlotsMiniDisplay(props: Props): ReactElement {
  const numDisplay = props.numDisplay ? props.numDisplay : 6;
  let slots = props.slots;
  if (slots.length > numDisplay) slots.length = numDisplay;

  return (
    <Box pt={1}>
      <Grid container>
        {props.slots.map((slot) => (
          <Grid item xs={4}>
            <SlotMiniDisplay
              dotColor={slot.colorDot}
              text={dateFormat(slot.date, "mmm")}
            />
          </Grid>
        ))}
      </Grid>
    </Box>
  );
}
